    dispose() {
        if (this.menuDialog) this.menuDialog.dispose();
        if (this.endDialog) this.endDialog.dispose();
        if (this.homeActionDialog) this.homeActionDialog.dispose();

        document.removeEventListener('mousedown', this.onDocumentMouseDown, false);
        document.removeEventListener('mousemove', this.onDocumentMouseMove, false);
        document.removeEventListener('touchdown', this.onDocumentMouseDown, false);

        this.disposeSuper();
    }

    updateForResize = () => {
        this.updateForResizeSuper();

        for (let i = 0; i < this.positionalObjs.length; ++i) {
            this.positionalObjs[i].updateForResize();
        }
    }

    createScene = () => {

        let backgroundImage = this.getTexture2(this.assetManager.textures[47], "background", true, 0, 0, 0, 0, -100, new THREE.Vector2(3, 1), THREE.RepeatWrapping);
        this.scene.add(backgroundImage.texture);

        let leftTree = this.getTexture2(this.assetManager.textures[49], "lefttree", true, -1, 133, 0, 0, -97);
        this.scene.add(leftTree.texture);
        this.positionalObjs.push(leftTree);

        let rightTree = this.getTexture2(this.assetManager.textures[48], "righttree", true, 1, -133, 0, 3, -97);
        this.scene.add(rightTree.texture);
        this.positionalObjs.push(rightTree);

        let bushLeft = this.getTexture2(this.assetManager.textures[50], "bushLeft", true, 1, -80, 0, -195, -95);
        this.scene.add(bushLeft.texture);
        this.positionalObjs.push(bushLeft);

        let bushRight = this.getTexture2(this.assetManager.textures[51], "bushRight", true, -1, 80, 0, -195, -95);
        this.scene.add(bushRight.texture);
        this.positionalObjs.push(bushRight);

        let bottomLand = this.getTexture2(this.assetManager.textures[52], "bottomland", true, 0, 0, -1, 18, -97, new THREE.Vector2(3, 1));
        this.scene.add(bottomLand.texture);
        this.positionalObjs.push(bottomLand);

        let board = this.getTexture2(this.assetManager.textures[53], "board", true, 0, 0, 0, -20, -80);
        this.scene.add(board.texture);

        this.eraser = this.getTexture2(this.assetManager.textures[46], "eraser", true, 0, 370, -1, -190, -1);
        this.scene.add(this.eraser.texture);

        this.updateForResize();

        document.addEventListener('mousedown', this.onDocumentMouseDown, false);
        document.addEventListener('mousemove', this.onDocumentMouseMove, false);

        document.addEventListener('touchdown', this.onDocumentMouseDown, false);

        this.goToNextStep();

        this.startBackgroundMusic();
    }

    update = () => {
        const delta = this.clock.getDelta();
        this.time += delta;

        this.updateSuper(delta);

        if (this.endDialog)
            this.endDialog.update();

        if(this.menuBar && this.step > 11){
            this.menuBar.enableBack(true);
            this.menuBar.enableHint(true);
        }
        if (this.waitingForTap && this.time >= this.waitSelectionDuration) {
            this.time = 0;
            this.voiceOutSelectionForChoosingShapes();
        }

    }

    onDocumentMouseDown = (event) => {
        if (!this.enableEvents)
            return;
        
        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.mouse, this.camera);

        var intersects = this.raycaster.intersectObjects(this.scene.children);

        if (intersects.length > 0) {
            let intersectName = intersects[0].object.name;
            let intersectObject = intersects[0].object;

            if(this.waitingForTap && intersectName == "eraser"){
                this.eraserClicked = true;
            }

            var names = ['paintBrown', 'paintPink', 'paintBlack', 'paintOrange', 'paintBlue', 'paintRed', 'paintPurple', 'paintYellow', 'paintGrey', 'paintGreen']

            if (!this.eraserClicked  && names.indexOf(intersectName) !== -1) {
                if (this.waitingForTap) {
                    if (this.colorHighlight) {
                        this.scene.remove(this.colorHighlight.texture);
                    }
                    this.validateShapeSelection(intersectName, intersectObject, false);
                }
            }
            if ( this.waitingForTap && (intersectName == "circle"
                || intersectName == "square"
                || intersectName == "triangle"
                || intersectName == "rectangle")) {
                    
                if(!this.eraserClicked) {
                    this.validateShapeSelection(intersectName, intersectObject, true);
                }else if(this.eraserClicked){
                    this.eraseColorOnShapes(intersectName);
                }
            }
        }
    }

    onDocumentMouseMove = (event) => {
        this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = - (event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;
    }

    onMenuBarLoaded = () => {
        this.menuBar.enableHome(true);
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableReward(true);
        this.menuBar.enableAudioPlayButton(true);
        this.menuBar.enableSkip(this.sdk.getCurrentLevel() > 3);

        this.onMenuBarLoadedSuper();
    }

    onHintAction = () => {
        this.stopAll();
        this.setEnableEvents(false);
        localStorage.setItem("stepOnHintclicked",this.step);
        this.checkpoint = this.step;
        this.waitingForTap = false;
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableSkip(true);
        this.step = 0;
        this.goToNextStep();
    }

    onHomeAction = () => {
        if (this.menuDialog || this.endDialog)
            return;

        this.stopAll();
        this.setEnableEvents(false);
        this.checkpoint = this.step;

        this.homeActionDialog = new HomeActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    onAudioReplayAction = () => {
        if(this.isAnyAudioPlaying()){
            return;
        }

        if (this.step > 11 && this.currentAudioPath )
            this.playAudioPath( this.currentAudioPath);
    }

    stopAll = () => {
        this.mascot.disable();
        this.removeAllShapes();
        if (this.hand) this.scene.remove(this.hand.texture);
        this.stopAllAudio();
        this.shapesPainted = [];
        this.correctAnswers = [];
        this.waitingForTap = false;
        this.waitingFor1 = "";
        this.waitingFor2 = "";
        this.timer.clearAll();
    }

