    removeAllShapes = () => {
        const thisObjects = this;
        const allShapes = [this.paintRed,this.paintBlue,this.paintYellow,this.paintGreen,
            this.paintOrange,this.paintPurple,this.paintBrown,this.paintPink,this.paintGrey,this.paintBlack,
            this.greenBrush,this.circle, this.triangle,this.rectangle,this.square, 
            this.computer, this.ship,this.rjStudio,this.radio,
            this.kitchen,this.ticketCounter,this.supermarket,this.photoFrame,this.nionLight,
            this.metroTrain,this.house,this.filmReel,this.suitcase,this.truck, this.busGlass,
            this.hand, this.colorHighlight];
        let scene = this.scene;

        allShapes.forEach(function (item) {
            if (item && item.texture) {
                scene.remove(item.texture);
                thisObjects.removeAsset(item.texture);
            }
        });
    }

    arrangeHorizontally = (textures, distanceBetween, y) => {
        console.log(textures);
        let totalWidth = 0;
        for (let i = 0; i < textures.length; ++i) {
            totalWidth += textures[i].getWidth();
        }

        console.log(totalWidth);
        totalWidth += (textures.length - 1) * distanceBetween;
        console.log(totalWidth);

        let x = -totalWidth / 2;
        for (let i = 0; i < textures.length; ++i) {
            x += textures[i].getWidth() / 2;
            textures[i].setNewPosition(0, x, 0, y);
            x += textures[i].getWidth() / 2 + distanceBetween;
        }
    }

    arrangeAllTheShapesAccordingly = (firstShape, x1,y1,secondShape,x2,y2,thirdShape,x3,y3,fourthShape,x4,y4) => {
        firstShape.setNewPosition(0,x1,0,y1);
        secondShape.setNewPosition(0,x2,0,y2);
        thirdShape.setNewPosition(0,x3,0,y3);
        fourthShape.setNewPosition(0,x4,0,y4);
    }

