import * as THREE from 'three'

class LoadingManager 
{
    constructor(scene){
        this.scene = scene;

        const geometry = new THREE.PlaneGeometry( 100, 10 );
        const material = new THREE.MeshBasicMaterial( {color: 0xffff00, side: THREE.DoubleSide} );
        const background = new THREE.Mesh( geometry, material );

        let filGeometry = new THREE.PlaneGeometry( 100, 10 );
        const fillMaterial = new THREE.MeshBasicMaterial( {color: 0x0000ff, side: THREE.DoubleSide} );
        let fill = new THREE.Mesh( filGeometry, fillMaterial );
        fill.position.add(new THREE.Vector3(50, 0, 0));
        
        this.fillGO = new THREE.Object3D();
        this.fillGO.add(fill);
        this.fillGO.position.x = -50;
        this.fillGO.scale.x = 0.0;
        
        this.loadingBar = new THREE.Object3D();
        this.loadingBar.add(background);   
        this.loadingBar.add(this.fillGO);       
        this.loadingManager = new THREE.LoadingManager();
        
        this.loadingManager.onLoad = () => {
            console.log("load ");    
        } 

        this.loadingManager.onProgress = ( url, itemsLoaded, itemsTotal ) => {
            console.log(`Items loaded: ${itemsLoaded}/${itemsTotal}`)
            // Update loading indicator
            if(!this.loadingBar.parent)
                this.scene.add( this.loadingBar );
            
            this.fillGO.scale.x = itemsLoaded/itemsTotal;
        }

        this.loadingManager.onError = (url) => {
            console.log("error")
        }

        this.loadingManager.onSceneLoaded = () => {
            console.log("onSceneLoaded")
            // Remove loading indicator
            this.scene.remove( this.loadingBar );
        }        
    }

    get obj(){
        return this.loadingManager;
    }

    dispose(){
    }
}

export default LoadingManager 
