import * as THREE from 'three'

class AnimatedTexture 
{
    constructor(texture, repeatx, repeaty, totalFrames, duration){
        this.texture = texture;
        this.repeatx = repeatx;
        this.repeaty = repeaty;
        this.totalFrames = totalFrames;
        this.current = 0;        
        this.clock = new THREE.Clock();
        this.time = 0;
        this.perFrameDuration = duration/totalFrames;

        this.texture.wrapS = this.texture.wrapT = THREE.RepeatWrapping; 
        this.texture.repeat.set( 1 / repeatx, 1 / repeaty );
    }

    update = () => {
        this.time += this.clock.getDelta();
        
        if(this.time >= this.perFrameDuration){
            this.time = 0;

            this.current += 1;
            if(this.current >= this.totalFrames){
                this.current = 0;
            }

            this.texture.offset.x = (this.current % this.repeatx)/this.repeatx;
            this.texture.offset.y =  (Math.floor(this.current / this.repeatx))/this.repeaty;
        }
    }
}

export default AnimatedTexture 