import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config'
import Utils from './Utils'
import AnimatedTexture from './AnimatedTexture'

class Mascot {
    constructor(loadingManager, scene, camera, renderer, sound,listner, callback) {
        this.imagePaths = [
            './textures/common/mascot/Mascot1.png',
            './textures/common/mascot/Mascot2.png',
            './textures/common/mascot/Mascot3.png',
            './textures/common/mascot/Mascot4.png',
            './textures/common/interactions/Speech_Bubble.png',
            './textures/common/talkinganimation.png'
        ];

        this.imageAssets = [];

        this.callback = callback;
        this.camera = camera;
        this.scene = scene;
        this.renderer = renderer;
        this.sound = sound;
        this.assetManager = new AssetManager();
        this.raycaster = new THREE.Raycaster();
        this.mouse = new THREE.Vector2();
        this.mascots = []
        this.loaded = false;
        this.mascotIndex = -1;

        this.talking = false;

        this.load(loadingManager);
    }

    dispose() {
        // Dispose only what is created here
        this.assetManager.dispose();

        for (let i = 0; i < this.imageAssets.length; ++i) {
            this.scene.remove(this.imageAssets[i]);
        }
    }

    load = async (loadingManager) => {
        console.log("Mascot loading...");

        // Load all assets. Textures, sounds, models required
        await this.assetManager.loadTextures(loadingManager, this.imagePaths);

        // Create scene
        this.createScene();

        loadingManager.onSceneLoaded();

        this.loaded = true;

        if (this.mascotIndex >= 0) {
            this.enable(this.mascotIndex, this.mascotPos);
        }
    }

    updateForResize = () => {
        for (let i = 0; i < this.mascots.length; ++i) {
            this.mascots[i].updateForResize();
        }
        if (this.bubble)
            this.bubble.updateForResize();
    }

    getTexture2 = (image, name, transparent, xref, x, yref, y, z, vec = null) => {
        var texture = Utils.getTexture(image, name, transparent, x, y, z, vec);
        this.imageAssets.push(texture);
        return new Texture(texture, xref, x, yref, y);
    }

    createScene = () => {
        this.mascot1 = this.getTexture2(this.assetManager.textures[0], "mascot1", true, 0, 0, -1, 100, 0);
        this.mascot2 = this.getTexture2(this.assetManager.textures[1], "mascot2", true, 0, 0, -1, 100, 0);
        this.mascot3 = this.getTexture2(this.assetManager.textures[2], "mascot3", true, 0, 0, -1, 100, 0);
        this.mascot4 = this.getTexture2(this.assetManager.textures[3], "mascot4", true, 0, 0, -1, 100, 0);
        this.mascots.push(this.mascot1);
        this.mascots.push(this.mascot2);
        this.mascots.push(this.mascot3);
        this.mascots.push(this.mascot4);

        this.bubble = this.getTexture2(this.assetManager.textures[4], "bubble", true, 0, 100, -1, 200, 0);

        // For animated Texture we have implement on own 
        const mat = new THREE.MeshBasicMaterial({ map: Texture.get2DTexture(this.assetManager.textures[5]) })
        mat.transparent = true;

        this.dotted = new THREE.Mesh(new THREE.PlaneGeometry(40, 40), mat)
        this.dotted.name = "talk_bubble_dots";
        this.dotted.position.x = 0;
        this.dotted.position.y = 0;
        this.dotted.position.z = 0;
        this.bubble.texture.add(this.dotted);

        this.dottedAnimatedTexture = new AnimatedTexture(this.assetManager.textures[5], 10, 3, 28, 2);

        this.updateForResize();
    }

    enableTalkingAnimation = (duration) => {
        this.talking = true;
        this.time = 0;
        this.duration = duration;
        this.scene.add(this.bubble.texture);
    }

    enable(index, pos = 0) {
        this.mascotIndex = index;
        this.mascotPos = pos;
        if (this.loaded) {
            for (let i = 0; i < this.mascots.length; ++i) {
                if (i == index)
                    this.scene.add(this.mascots[i].texture);
                else
                    this.scene.remove(this.mascots[i].texture);
            }

            switch (this.mascotPos) {
                case 0:
                    this.mascots[this.mascotIndex].setNewPosition(0, 0, -1, 100);
                    this.bubble.setScale(1, 1);
                    this.bubble.setNewPosition(0, 100, -1, 200);
                    break;
                case 1:
                    this.mascots[this.mascotIndex].setNewPosition(1, -70, -1, 100);
                    this.bubble.setScale(-1, 1);
                    this.bubble.setNewPosition(1, -150, -1, 200);
                    break;
            }
        }
    }

    disable() {
        for (let i = 0; i < this.mascots.length; ++i) {
            this.scene.remove(this.mascots[i].texture);
        }
        if (this.bubble) this.scene.remove(this.bubble.texture);
    }

    update = (delta) => {
        if (this.dottedAnimatedTexture)
            this.dottedAnimatedTexture.update();

        if(this.talking){
            this.time += delta;
            if(this.time >= this.duration){
                this.talking = false;
                this.scene.remove(this.bubble.texture);
            }
        }
    }
}

export default Mascot