import * as THREE from 'three'
import Texture from './Texture'
import Config from './Config' 

class Utils 
{
    static getTexture = (image, name, transparent, x, y, z, vec=null, wrappingS=null, wrappingT=null) => {
        const texture = Texture.get2DTexture(image, vec);
        if(wrappingS)
            texture.wrapS = wrappingS;
        if(wrappingT)
            texture.wrapT = wrappingT;
        const mat = new THREE.MeshBasicMaterial({ map: texture})
        mat.transparent = transparent;
        const xMult = (vec == null) ? 1 : vec.x;
        const yMult = (vec == null) ? 1 : vec.y;
        const mesh = new THREE.Mesh( new THREE.PlaneGeometry( Texture.width(image)*xMult, Texture.height(image)*yMult), mat)
        mesh.name = name;
        mesh.position.x = x;
        mesh.position.y = y;
        mesh.position.z = z;
        
        return mesh;
    }

    static getTextureFromAtlas = (image, name, transparent, x, y, z, sx, sy, repeatx, repeaty) => {
        const mat = new THREE.MeshBasicMaterial({ map: Texture.get2DTexture(image) })
        mat.transparent = transparent;

        image.wrapS = image.wrapT = THREE.RepeatWrapping; 
        image.repeat.set( 1 / repeatx, 1 / repeaty );

        const mesh = new THREE.Mesh( new THREE.PlaneGeometry( sx, sy), mat)
        mesh.name = name;
        mesh.position.x = x;
        mesh.position.y = y;
        mesh.position.z = z;

        return mesh;
    }

    static getWidth = () => {
        return (window.innerWidth) * (Config.frustumSize/window.innerHeight)
    }

    static getLeftBorder =() => {
        return - (window.innerWidth/2) * (Config.frustumSize/window.innerHeight);
    }

    static getRightBorder = () => {
        return (window.innerWidth/2) * (Config.frustumSize/window.innerHeight);
    }

    static getTopBorder =() => {
        return Config.frustumSize/2;
    }

    static getBottomBorder = () => {
        return -Config.frustumSize/2;
    }

    static randomized = (arr) => {
        let randArr = []; 
        while(arr.length > 0){
            let index = Math.floor(Math.random()*arr.length);            
            randArr.push(arr[index]);
            arr.splice(index, 1);
        }        
        return randArr;
    }
}

export default Utils 