import * as THREE from 'three'

class Logs {
    constructor(callback, sdk) {
        this.sdk = sdk;
    }

    logEventLevelStartDuration = (levelNum) => {   
        switch(levelNum){
            case 0:
                let startTimeSpentLevel0 = Math.floor(Date.now()/1000);
                localStorage.setItem("startTimeSpentLevel0", startTimeSpentLevel0);
                break;
            case 1:
                let startTimeSpentLevel1 = Math.floor(Date.now()/1000);
                localStorage.setItem("startTimeSpentLevel1", startTimeSpentLevel1);
                break;
            case 2:
                let startTimeSpentLevel2 = Math.floor(Date.now()/1000);
                localStorage.setItem("startTimeSpentLevel2", startTimeSpentLevel2);
                break;
            case 3:
                let startTimeSpentLevel3 = Math.floor(Date.now()/1000);
                localStorage.setItem("startTimeSpentLevel3", startTimeSpentLevel3);
                break;
            case 4:
                let startTimeSpentLevel4 = Math.floor(Date.now()/1000);
                localStorage.setItem("startTimeSpentLevel4", startTimeSpentLevel4);
                break;
        }
        
    }

    logEventLevelEndDuration = (levelNum) =>{
        switch(levelNum){
            case 0:
                let endTimeSpentLevel0 = Math.floor(Date.now()/1000);
                const startTimeSpentLoaded = parseInt(localStorage.getItem("startTimeSpentLevel0"));
                const timeSpentL0 = endTimeSpentLevel0 - startTimeSpentLoaded;
                console.log(timeSpentL0);
                this.sdk.setLevelCompleted(levelNum, timeSpentL0, false);
                break;
            case 1:
                let endTimeSpentLevel1 = Math.floor(Date.now()/1000);
                const startTimeSpentLoaded1 = parseInt(localStorage.getItem("startTimeSpentLevel1"));
                const timeSpentL1 = endTimeSpentLevel1 - startTimeSpentLoaded1;
                console.log(timeSpentL1);
                this.sdk.setLevelCompleted(levelNum, timeSpentL1, true);
                break;
            case 2: 
                let endTimeSpentLevel2 = Math.floor(Date.now()/1000);
                const startTimeSpentLoaded2 = parseInt(localStorage.getItem("startTimeSpentLevel2"));
                const timeSpentL2 = endTimeSpentLevel2 - startTimeSpentLoaded2;
                console.log(timeSpentL2);
                this.sdk.setLevelCompleted(levelNum, timeSpentL2, true);
                break;
            case 3:
                let endTimeSpentLevel3= Math.floor(Date.now()/1000);
                const startTimeSpentLoaded3 = parseInt(localStorage.getItem("startTimeSpentLevel3"));
                const timeSpentL3 = endTimeSpentLevel3- startTimeSpentLoaded3;
                console.log(timeSpentL3);
                this.sdk.setLevelCompleted(levelNum, timeSpentL3, true);
                break;
            case 4:
                let endTimeSpentLevel4= Math.floor(Date.now()/1000);
                const startTimeSpentLoaded4 = parseInt(localStorage.getItem("startTimeSpentLevel4"));
                const timeSpentL4 = endTimeSpentLevel4- startTimeSpentLoaded4;
                console.log(timeSpentL4);

                this.sdk.setLevelCompleted(levelNum, timeSpentL4, true);
        }
    }

    logEventPresentationStartDuration = (levelNum) =>{
        switch(levelNum){
            case 1:
                if(this.sdk.getPresentationCompleteForLevel(levelNum) == 1){
                    let presentTime = Math.floor(Date.now()/1000);
                    let getStartPresentBefore = this.sdk.getPresentationTimespent(levelNum);
                    let startPresentationTimeSpentLevel1 = presentTime - getStartPresentBefore;
                    localStorage.setItem("startPresentationTimeSpentLevel1", startPresentationTimeSpentLevel1);
                }else{
                    let startPresentationTimeSpentLevel1 = Math.floor(Date.now()/1000);
                    localStorage.setItem("startPresentationTimeSpentLevel1", startPresentationTimeSpentLevel1);
                }
                break;
            case 2:
                if(this.sdk.getPresentationCompleteForLevel(levelNum) == 1){
                    let presentTime = Math.floor(Date.now()/1000);
                    let getStartPresentBefore = this.sdk.getPresentationTimespent(levelNum);
                    let startPresentationTimeSpentLevel2 = presentTime - getStartPresentBefore;
                    localStorage.setItem("startPresentationTimeSpentLevel2", startPresentationTimeSpentLevel2);
                }else{
                    let startPresentationTimeSpentLevel2 = Math.floor(Date.now()/1000);
                    localStorage.setItem("startPresentationTimeSpentLevel2", startPresentationTimeSpentLevel2);
                }
                break;
            case 3:
                if(this.sdk.getPresentationCompleteForLevel(levelNum) == 1){
                    let presentTime = Math.floor(Date.now()/1000);
                    let getStartPresentBefore = this.sdk.getPresentationTimespent(levelNum);
                    let startPresentationTimeSpentLevel3 = presentTime - getStartPresentBefore;
                    localStorage.setItem("startPresentationTimeSpentLevel3", startPresentationTimeSpentLevel3);
                }else{
                    let startPresentationTimeSpentLevel3 = Math.floor(Date.now()/1000);
                    localStorage.setItem("startPresentationTimeSpentLevel3", startPresentationTimeSpentLevel3);
                }
                break;
            case 4:
                if(this.sdk.getPresentationCompleteForLevel(levelNum) == 1){
                    let presentTime = Math.floor(Date.now()/1000);
                    let getStartPresentBefore = this.sdk.getPresentationTimespent(levelNum);
                    let startPresentationTimeSpentLevel4 = presentTime - getStartPresentBefore;
                    localStorage.setItem("startPresentationTimeSpentLevel4", startPresentationTimeSpentLevel4);
                }else{
                    let startPresentationTimeSpentLevel4 = Math.floor(Date.now()/1000);
                    localStorage.setItem("startPresentationTimeSpentLevel4", startPresentationTimeSpentLevel4);
                }
                break;
        }
        this.sdk.setLevelStarted(levelNum);
    }

    logEventPresentationEndDuration = (levelNum) =>{
        switch(levelNum){
            case 1:
                let presentationTimeSpentLevel1= Math.floor(Date.now()/1000);
                localStorage.setItem("endPresentationTimeSpentL1", presentationTimeSpentLevel1);
                const startTimeSpentLoadedL1 = parseInt(localStorage.getItem("startPresentationTimeSpentLevel1"));
                const presentationTimeSpentL1 = presentationTimeSpentLevel1 - startTimeSpentLoadedL1;
                console.log(presentationTimeSpentL1);
                this.sdk.setPresentationCompleted(levelNum, presentationTimeSpentL1);
                break;
            case 2:
                let presentationTimeSpentLevel2= Math.floor(Date.now()/1000);
                localStorage.setItem("endPresentationTimeSpentL2", presentationTimeSpentLevel2);

                const startTimeSpentLoadedL2 = parseInt(localStorage.getItem("startPresentationTimeSpentLevel2"));
                const presentationTimeSpentL2 = presentationTimeSpentLevel2 - startTimeSpentLoadedL2;
                console.log(presentationTimeSpentL2);

                this.sdk.setPresentationCompleted(levelNum, presentationTimeSpentL2);
                break;
            case 3:
                let presentationTimeSpentLevel3 = Math.floor(Date.now() / 1000);
                localStorage.setItem("endPresentationTimeSpentL3", presentationTimeSpentLevel3);
                const startTimeSpentLoadedL3 = parseInt(localStorage.getItem("startPresentationTimeSpentLevel3"));
                const presentationTimeSpentL3 = presentationTimeSpentLevel3 - startTimeSpentLoadedL3;
                console.log(presentationTimeSpentL3);

                this.sdk.setPresentationCompleted(levelNum, presentationTimeSpentL3);
                break;
            case 4:
                let presentationTimeSpentLevel4 = Math.floor(Date.now() / 1000);
                localStorage.setItem("endPresentationTimeSpentL4", presentationTimeSpentLevel4);
                const startTimeSpentLoadedL4 = parseInt(localStorage.getItem("startPresentationTimeSpentLevel4"));
                const presentationTimeSpentL4 = presentationTimeSpentLevel4 - startTimeSpentLoadedL4;
                console.log(presentationTimeSpentL4);

                this.sdk.setPresentationCompleted(levelNum, presentationTimeSpentL4);
                break;
        }
        
    }
}

export default Logs