import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config' 
import Utils from './Utils'
import LevelProgressBar from './LevelProgressBar'
import {Text} from 'troika-three-text'

class MenuBar 
{
    constructor(loadingManager, scene, camera, renderer, sound, backgroundSound, listner, callback, sdk){
        this.imagePaths = [
            './textures/common/progress_bar/Progress_bar_Type_3.png',
            './textures/common/interactions/Home_Inactive.png',
            './textures/common/interactions/Home.png',
            './textures/common/interactions/Back_Inactive.png',
            './textures/common/interactions/Back.png',
            './textures/common/interactions/Music_Mute.png',
            './textures/common/interactions/Music.png',
            './textures/common/interactions/Hint_Inactive.png',
            './textures/common/interactions/Hint.png',
            './textures/common/interactions/Rewards_Inactive.png',
            './textures/common/interactions/Rewards.png',
            './textures/common/interactions/Audio_Replay.png',
            './textures/common/interactions/Skip.png',
            './textures/common/progress_bar/Progress_Bar_Type2_element_blue_transparent.png',
            './textures/common/interactions/Hand_Cursor3.png',
            './textures/common/progress_bar/Black_Text_Level_1.png',
            './textures/common/progress_bar/Black_Text_Level_2.png',
            './textures/common/progress_bar/Black_Text_Level_3.png',
            './textures/common/progress_bar/Black_Text_Level_4.png',
            ];
        
        this.imageAssets = [];

        this.sdk = sdk;

        console.log("----- Menubar ------");
        console.log(this.sdk);
        console.log(sdk);

        this.callback = callback;
        this.showAudioReplay = false;
        this.camera = camera;
        this.scene = scene;
        this.renderer = renderer;
        this.sound = sound;
        this.backgroundSound = backgroundSound;

        this.enableEvents = true;

        this.assetManager = new AssetManager();
        this.raycaster = new THREE.Raycaster(); 
        this.mouse = new THREE.Vector2();
        this.load(loadingManager);
    }

    dispose(){
        // Dispose only what is created here
        this.assetManager.dispose();

        if(this.rewardText){
            this.scene.remove(this.rewardText);
            this.rewardText.dispose();    
        }

        document.removeEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.removeEventListener( 'touchdown', this.onDocumentMouseDown, false );

        for(let i=0; i<this.imageAssets.length; ++i){
            this.scene.remove(this.imageAssets[i]);
        }

        if(this.level1ProgressBar) this.scene.remove(this.level1ProgressBar.obj);
        if(this.level2ProgressBar) this.scene.remove(this.level2ProgressBar.obj);
        if(this.level3ProgressBar) this.scene.remove(this.level3ProgressBar.obj);
        if(this.level4ProgressBar) this.scene.remove(this.level4ProgressBar.obj);
    }

    load = async(loadingManager) => {
        console.log("MenuBar loading...");
        
        // Load all assets. Textures, sounds, models required
        await this.assetManager.loadTextures(loadingManager, this.imagePaths);

        // Create scene
        this.createScene();
        
        loadingManager.onSceneLoaded();
    }

    updateForResize = () =>{
        if(Utils.getWidth() < Config.refWidth) {
            if(this.progressbar) this.progressbar.position.x = -60;
            if(this.progressbar_selectionL1) this.progressbar_selectionL1.position.x = -94-60;
            if(this.progressbar_selectionL2) this.progressbar_selectionL2.position.x = -31-60;
            if(this.progressbar_selectionL3) this.progressbar_selectionL3.position.x = 32-60;
            if(this.progressbar_selectionL4) this.progressbar_selectionL4.position.x = 94-60;

            if(this.progressbar_levelBorderL1) this.progressbar_levelBorderL1.position.x = -93.3-60;
            if(this.progressbar_levelBorderL2) this.progressbar_levelBorderL2.position.x = -30.3-60;
            if(this.progressbar_levelBorderL3) this.progressbar_levelBorderL3.position.x = 33-60;
            if(this.progressbar_levelBorderL4) this.progressbar_levelBorderL4.position.x = 95-60;

            if(this.level1ProgressBar) this.level1ProgressBar.obj.position.x = -94-60;
            if(this.level2ProgressBar) this.level2ProgressBar.obj.position.x = -31-60;
            if(this.level3ProgressBar) this.level3ProgressBar.obj.position.x = 31-60;
            if(this.level4ProgressBar) this.level4ProgressBar.obj.position.x = 94-60;
        }
        else{
            if(this.progressbar) this.progressbar.position.x = 0;
            if(this.progressbar_selectionL1) this.progressbar_selectionL1.position.x = -94;
            if(this.progressbar_selectionL2) this.progressbar_selectionL2.position.x = -31;
            if(this.progressbar_selectionL3) this.progressbar_selectionL3.position.x = 32;
            if(this.progressbar_selectionL4) this.progressbar_selectionL4.position.x = 94; 
            
            if(this.progressbar_levelBorderL1) this.progressbar_levelBorderL1.position.x = -93.3;
            if(this.progressbar_levelBorderL2) this.progressbar_levelBorderL2.position.x = -30.3;
            if(this.progressbar_levelBorderL3) this.progressbar_levelBorderL3.position.x = 33;
            if(this.progressbar_levelBorderL4) this.progressbar_levelBorderL4.position.x = 95;

            if(this.level1ProgressBar) this.level1ProgressBar.obj.position.x = -94;
            if(this.level2ProgressBar) this.level2ProgressBar.obj.position.x = -31;
            if(this.level3ProgressBar) this.level3ProgressBar.obj.position.x = 31;
            if(this.level4ProgressBar) this.level4ProgressBar.obj.position.x = 94;
        }

        if(this.homeButton) this.homeButton.position.x = Utils.getLeftBorder()+30;
        if(this.homeInactiveButton) this.homeInactiveButton.position.x = Utils.getLeftBorder()+30;
        if(this.backButton) this.backButton.position.x = Utils.getLeftBorder()+85;
        if(this.backInactiveButton) this.backInactiveButton.position.x = Utils.getLeftBorder()+85;
        if(this.musicButton) this.musicButton.position.x = Utils.getRightBorder()-215;
        if(this.musicInactiveButton) this.musicInactiveButton.position.x = Utils.getRightBorder()-215;
        if(this.hintButton) this.hintButton.position.x = Utils.getRightBorder()-160;
        if(this.hintInactiveButton) this.hintInactiveButton.position.x = Utils.getRightBorder()-160;
        if(this.rewardButton) this.rewardButton.position.x = Utils.getRightBorder()-65;
        if(this.rewardInactiveButton) this.rewardInactiveButton.position.x = Utils.getRightBorder()-65;
        if(this.audioReplayButton) this.audioReplayButton.position.x = Utils.getRightBorder()-30;
        if(this.replayHand) this.replayHand.position.x = Utils.getRightBorder()-20;
        if(this.skipButton) this.skipButton.position.x = Utils.getRightBorder()-30;

        if(this.rewardText){
            this.rewardText.position.x = this.rewardButton.position.x+20;
            this.rewardText.sync()    
        }
    }

    getTexture = (image, name, transparent, x, y, z, vec=null) => {        
        var asset = Utils.getTexture(image, name, transparent, x, y, z, vec);
        this.imageAssets.push(asset);
        return asset;
    }

    createScene = () =>{
        // progress bar
        this.progressbar = this.getTexture(this.assetManager.textures[0], "progress_bar", true, 0, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.progressbar)

        // // progressbar_selection
        this.progressbar_selectionL1 = this.getTexture(this.assetManager.textures[13], "progressbar_selection", true, -94, Config.frustumSize/2 - 39, 0.1);

        this.progressbar_selectionL2 = this.getTexture(this.assetManager.textures[13], "progressbar_selection", true, -31, Config.frustumSize/2 - 39, 0.1);

        this.progressbar_selectionL3 = this.getTexture(this.assetManager.textures[13], "progressbar_selection", true, 32, Config.frustumSize/2 - 39, 0.1);

        this.progressbar_selectionL4 = this.getTexture(this.assetManager.textures[13], "progressbar_selection", true, 94, Config.frustumSize/2 - 39, 0.1);

        // // progressbar name_selection
        this.progressbar_levelBorderL1 = this.getTexture(this.assetManager.textures[15], "progressbar_levelBorderL1", true, -93.3, Config.frustumSize/2 - 23.5, 0.1);

        this.progressbar_levelBorderL2 = this.getTexture(this.assetManager.textures[16], "progressbar_levelBorderL2", true, -30.3, Config.frustumSize/2 - 23.5, 0.1);

        this.progressbar_levelBorderL3 = this.getTexture(this.assetManager.textures[17], "progressbar_levelBorderL3", true, 33, Config.frustumSize/2 - 23.5, 0.1);

        this.progressbar_levelBorderL4 = this.getTexture(this.assetManager.textures[18], "progressbar_levelBorderL4", true, 95, Config.frustumSize/2 - 23.5, 0.1);

        // home button 
        this.homeButton = this.getTexture(this.assetManager.textures[2], "home_button", true, Utils.getLeftBorder()+30, Config.frustumSize/2 - 30, -1);
        //this.scene.add(this.homeButton)

        // home button inactive
        this.homeInactiveButton = this.getTexture(this.assetManager.textures[1], "home_button_inactive", true, Utils.getLeftBorder()+30, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.homeInactiveButton)

        // home button 
        this.backButton = this.getTexture(this.assetManager.textures[4], "back_button", true, Utils.getLeftBorder()+85, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.backButton);

        // home button inactive
        this.backInactiveButton = this.getTexture(this.assetManager.textures[3], "back_button_inactive", true, Utils.getLeftBorder()+85, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.backInactiveButton);

        // music button 
        this.musicButton = this.getTexture(this.assetManager.textures[6], "music_button", true, Utils.getRightBorder()-215, Config.frustumSize/2 - 30, -1);

        // music button inactive
        this.musicInactiveButton = this.getTexture(this.assetManager.textures[5], "music_button_mute", true, Utils.getRightBorder()-215, Config.frustumSize/2 - 30, -1);

        // hint button 
        this.hintButton = this.getTexture(this.assetManager.textures[8], "hint_button", true, Utils.getRightBorder()-160, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.hintButton);

        // hint button inactive
        this.hintInactiveButton = this.getTexture(this.assetManager.textures[7], "hint_button_inactive", true, Utils.getRightBorder()-160, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.hintInactiveButton);

        // reward button 
        this.rewardButton = this.getTexture(this.assetManager.textures[10], "reward_button", true, Utils.getRightBorder()-65, Config.frustumSize/2 - 30, -1);
        this.scene.add(this.rewardButton);

        // reward button inactive
        this.rewardInactiveButton = this.getTexture(this.assetManager.textures[9], "reward_button_inactive", true, Utils.getRightBorder()-65, Config.frustumSize/2 - 30, -1);
        // this.scene.add(this.rewardInactiveButton);
     
        this.audioReplayButton = this.getTexture(this.assetManager.textures[11], "audio_replay", true, Utils.getRightBorder()-30, Config.frustumSize/2 - 90, -1);

        this.skipButton = this.getTexture(this.assetManager.textures[12], "skip_button", true, Utils.getRightBorder()-30, Config.frustumSize/2 - 160, -1);

        this.replayHand = this.getTexture(this.assetManager.textures[14], "replayHand", true, Utils.getRightBorder()-20, Config.frustumSize/2 - 130, -1);
        this.replayHand.scale.x = 0.4;
        this.replayHand.scale.y = 0.4;

        this.rewardText = new Text()
        this.scene.add(this.rewardText)
        this.rewardText.anchorX = 'center';
        this.rewardText.fontSize = 15;
        this.rewardText.position.x = this.rewardButton.position.x+20;
        this.rewardText.position.y = this.rewardButton.position.y;
        this.rewardText.position.z = 0
        this.rewardText.color = 0xFFFFFF
        this.updateRewardText(this.sdk.getTotalRewardPoints());

        var vol = localStorage.getItem("volume");
        console.log("vol: "+vol);
        this.enableMusic(parseFloat(vol) > 0);

        this.level1ProgressBar = new LevelProgressBar(this.scene, -94, Config.frustumSize/2 - 39, -1);
        this.level2ProgressBar = new LevelProgressBar(this.scene, -31, Config.frustumSize/2 - 39, -1);
        this.level3ProgressBar = new LevelProgressBar(this.scene, 31, Config.frustumSize/2 - 39, -1);
        this.level4ProgressBar = new LevelProgressBar(this.scene, 94, Config.frustumSize/2 - 39, -1);

        this.updateForResize();        

        document.addEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.addEventListener( 'touchdown', this.onDocumentMouseDown, false );

        this.callback.onMenuBarLoaded();
    }     

    setEnableEvents = (bEnable) => {
        this.enableEvents = bEnable;
    }
    
    update = () => {

    }

    updateRewardText = (rewardNum) => {
        this.rewardText.text = '' + rewardNum;

        // Update the rendering:
        this.rewardText.sync()
    }

    enableAudioPlayButton = (bEnable) => {
        this.scene.remove(this.audioReplayButton);
        this.showAudioReplay = bEnable;
        if(bEnable){
            this.scene.add(this.audioReplayButton);
        }
    }    

    enableReward = (bEnable) => {
        if(bEnable){
            this.scene.add(this.rewardButton);
            this.scene.remove(this.rewardInactiveButton);
        }
        else{
            this.scene.remove(this.rewardButton);
            this.scene.add(this.rewardInactiveButton);            
        }
    }    

    enableMusic = (bEnable) => {
        this.scene.remove(this.musicButton);
        this.scene.remove(this.musicInactiveButton);
        if(bEnable){
            this.scene.add(this.musicButton);
        }
        else{
            this.scene.add(this.musicInactiveButton);            
        }

        console.log("----")
        console.log(this.sdk);
        if(this.sdk)
            this.sdk.setMusicButtonClicked(bEnable);
    }    

    enableHint = (bEnable) => {
        if(bEnable){
            this.scene.add(this.hintButton);
            this.scene.remove(this.hintInactiveButton);
        }
        else{
            this.scene.remove(this.hintButton);
            this.scene.add(this.hintInactiveButton);            
        }
    }    

    enableHome = (bEnable) => {
        if(bEnable){
            this.scene.add(this.homeButton);
            this.scene.remove(this.homeInactiveButton);
        }
        else{
            this.scene.remove(this.homeButton);
            this.scene.add(this.homeInactiveButton);            
        }
    }    

    enableReplayHand = (bEnable) => {
        if(bEnable){
            this.scene.add(this.replayHand);
        }
        else{
            this.scene.remove(this.replayHand);
        }
    }
    
    enablePresentLevelBorder1 = (bEnable) => {
        if(bEnable){
            this.scene.add(this.progressbar_selectionL1);
            this.scene.add(this.progressbar_levelBorderL1);
        }
        else{
            this.scene.remove(this.progressbar_selectionL1);
            this.scene.remove(this.progressbar_levelBorderL1);
        }
    }

    enablePresentLevelBorder2 = (bEnable) => {
        if(bEnable){
            this.scene.add(this.progressbar_selectionL2);
            this.scene.add(this.progressbar_levelBorderL2);
        }
        else{
            this.scene.remove(this.progressbar_selectionL2);
            this.scene.remove(this.progressbar_levelBorderL2);
        }
    }

    enablePresentLevelBorder3 = (bEnable) => {
        if(bEnable){
            this.scene.add(this.progressbar_selectionL3);
            this.scene.add(this.progressbar_levelBorderL3);
        }
        else{
            this.scene.remove(this.progressbar_selectionL3);
            this.scene.remove(this.progressbar_levelBorderL3);
        }
    }

    enablePresentLevelBorder4 = (bEnable) => {
        if(bEnable){
            this.scene.add(this.progressbar_selectionL4);
            this.scene.add(this.progressbar_levelBorderL4);
        }
        else{
            this.scene.remove(this.progressbar_selectionL4);
            this.scene.remove(this.progressbar_levelBorderL4);
        }
    }

    enableBack = (bEnable) => {
        if(bEnable){
            this.scene.add(this.backButton);
            this.scene.remove(this.backInactiveButton);
        }
        else{
            this.scene.remove(this.backButton);
            this.scene.add(this.backInactiveButton);            
        }
    }    

    enableSkip = (bEnable) => {
        this.scene.remove(this.skipButton);
        if(bEnable){
            this.scene.add(this.skipButton);
        }
    }    

    onDocumentMouseDown = ( event ) => {
        if(!this.enableEvents)
            return;

        this.mouse.x = ( event.clientX / this.renderer.domElement.clientWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / this.renderer.domElement.clientHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.mouse, this.camera );

        var intersects = this.raycaster.intersectObjects( this.scene.children );

        if ( intersects.length > 0 )
        {
            switch(intersects[0].object.name){
                case "music_button":
                    this.enableMusic(false);
                    localStorage.setItem("volume", "0");
                    this.backgroundSound.setVolume(0);
                    break;
                case "music_button_mute":
                    this.enableMusic(true);
                    localStorage.setItem("volume", "1");
                    this.backgroundSound.setVolume(1/2);
                    break;
                case "hint_button":
                    this.callback.onHintAction();
                    break;
                case "home_button":
                    this.callback.onHomeAction();
                    break;
                case "back_button":
                    this.callback.onBackAction();
                    break;
                case "audio_replay":
                    this.callback.onAudioReplayAction();
                    break;
                case "skip_button":
                    this.callback.onSkipAction();
                    break;
            }
        }
    }    
}

export default MenuBar 