import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Utils from './Utils'
import MenuBar from './MenuBar'
import Timer from './Timer'
import Mascot from './Mascot'
import SAudio from './SAudio'
import HomeActionDialog from './HomeActionDialog'
import HintCompleteDialog from './HintCompleteDialog'
import SkipActionDialog from './SkipActionDialog'
import BackActionDialog from './BackActionDialog'
import party from "./party.min.js";

class Level {
    constructor(loadingManager, scene, camera, renderer, sound, backgroundSound,listner, imagePaths, showAudioReplay=true, sdk) {
        this.imagePaths = imagePaths;

        this.imageAssets = [];

        this.sdk = sdk;
        this.loadingManager = loadingManager;
        this.camera = camera;
        this.scene = scene;
        this.renderer = renderer;
        this.sound = sound;
        this.backgroundSound = backgroundSound;
        this.assetManager = new AssetManager();
        this.raycaster = new THREE.Raycaster();
        this.mouse = new THREE.Vector2();
        this.mascot = null;
        this.lerpingObjs = [];
        this.blinkingObjs = [];
        this.clock = new THREE.Clock();
        this.audioPlayers = [];
        this.timer = new Timer(this);

        this.enableEvents = true;
        this.listener = listner
        this.audioLoader = new THREE.AudioLoader();

        this.load(loadingManager);

        this.menuBar = new MenuBar(loadingManager, scene, camera, renderer, sound, backgroundSound,listner, this, sdk);
        this.mascot = new Mascot(loadingManager, scene, camera, renderer, sound, null);
    }

    disposeSuper() {
        // Dispose only what is created here
        this.assetManager.dispose();
        this.menuBar.dispose();
        this.mascot.dispose();
        this.stopAllAudio();

        for (let i = 0; i < this.imageAssets.length; ++i) {
            this.scene.remove(this.imageAssets[i]);
        }
    }

    removeAsset = (image) => {
        const index = this.imageAssets.indexOf(image);
        if (index > -1) {
            this.imageAssets.splice(index, 1);
        }
    }

    // audioPlaybackEnded = (audio) => {
    //     for (let i = 0; i < this.audioPlayers.length; ++i) {
    //         if (audio.uuid == this.audioPlayers[i].uuid) {
    //             this.audioPlayers.splice(i, 1);
    //             audio.dispose();
    //             break;
    //         }
    //     }
    // }

    isAnyAudioPlaying = () => {
        for (let i = 0; i < this.audioPlayers.length; ++i) {
            if(this.audioPlayers[i].isAudioPlaying())
                return true;
        }
        return false;
    }

    cleaupAudioEnded = () => {
        for (let i = 0; i < this.audioPlayers.length; ++i) {
            if(this.audioPlayers[i].isAudioPlaying() == false){
                let audio = this.audioPlayers[i];
                this.audioPlayers.splice(i, 1);
                audio.dispose();
                break;
            }
        }
    }

    load = async (loadingManager) => {
        console.log("Level loading...");

        // Load all assets. Textures, sounds, models required
        await this.assetManager.loadTextures(loadingManager, this.imagePaths);

        // Create scene
        this.createScene();

        loadingManager.onSceneLoaded();

    }

    setEnableEvents = (bEnable) => {
        this.enableEvents = bEnable;
        this.menuBar.setEnableEvents(bEnable);
    }

    startBackgroundMusic = () => {
        let audioFilesMusic = ['./audio/common/Background_music_1.mp3', './audio/common/Background_music_2.mp3'];
        let index = Math.floor(Math.random()*2);
        this.playBackgroundMusic(true, audioFilesMusic[index]);
    }

    updateForResizeSuper = () => {
        if (this.menuBar)
            this.menuBar.updateForResize();
        if (this.mascot)
            this.mascot.updateForResize();
    }

    getTexture = (image, name, transparent, x, y, z, vec = null, wrappingS=null, wrappingT=null) => {
        var asset = Utils.getTexture(image, name, transparent, x, y, z, vec, wrappingS, wrappingT);
        this.imageAssets.push(asset);
        return asset;
    }

    getTexture2 = (image, name, transparent, xref, x, yref, y, z, vec = null, wrappingS=null, wrappingT=null) => {
        let texture = this.getTexture(image, name, transparent, x, y, z, vec, wrappingS, wrappingT);
        return new Texture(texture, xref, x, yref, y);
    }

    createScene = () => {
       
    }

    onMenuBarLoadedSuper = () => {
        if(this.levelNum == 0 && this.sdk.getIfLevelIsCompleted(1) == 1 && this.sdk.getIfLevelIsCompleted(2) == 1 && this.sdk.getIfLevelIsCompleted(3)== 1 && this.sdk.getIfLevelIsCompleted(4) == 1)
        {  
            this.menuBar.level1ProgressBar.setProgress(1.0);
            this.menuBar.level2ProgressBar.setProgress(1.0);
            this.menuBar.level3ProgressBar.setProgress(1.0);
            this.menuBar.level4ProgressBar.setProgress(1.0);
        }
        if(localStorage.getItem("level1progress")){
            let progress = 0;
            if(this.sdk.getCurrentLevel() == 1)
                progress = parseFloat(localStorage.getItem("level1progress"));
            else if (this.sdk.getIfLevelIsCompleted(1) == 1)
                progress = 1.0;
            this.menuBar.level1ProgressBar.setProgress(progress);
        }
        if(localStorage.getItem("level2progress")){
            let progress = 0;
            if(this.sdk.getCurrentLevel() == 2)
                progress = parseFloat(localStorage.getItem("level2progress"));
            else if (this.sdk.getIfLevelIsCompleted(2) == 1)
                progress = 1.0;
            this.menuBar.level2ProgressBar.setProgress(progress);
        }
        if(localStorage.getItem("level3progress")){
            let progress = 0;
            if(this.sdk.getCurrentLevel() == 3)
                progress = parseFloat(localStorage.getItem("level3progress"));
            else if (this.sdk.getIfLevelIsCompleted(3)== 1)
                progress = 1.0;
            this.menuBar.level3ProgressBar.setProgress(progress);
        }
        if(localStorage.getItem("level4progress")){
            let progress = 0;
            if(this.sdk.getPresentationCompleteForLevel(4) == 1)
                progress = parseFloat(localStorage.getItem("level4progress"));
            else if (this.sdk.getIfLevelIsCompleted(4) == 1)
                progress = 1.0;
            this.menuBar.level4ProgressBar.setProgress(progress);
        }

        if(this.levelNum == 1){
            this.menuBar.enablePresentLevelBorder1(true);
            this.menuBar.enablePresentLevelBorder2(false);
            this.menuBar.enablePresentLevelBorder3(false);
            this.menuBar.enablePresentLevelBorder4(false);
        }else if(this.levelNum == 2){
            this.menuBar.enablePresentLevelBorder1(false);
            this.menuBar.enablePresentLevelBorder2(true);
            this.menuBar.enablePresentLevelBorder3(false);
            this.menuBar.enablePresentLevelBorder4(false);
        }else if(this.levelNum == 3){
            this.menuBar.enablePresentLevelBorder1(false);
            this.menuBar.enablePresentLevelBorder2(false);
            this.menuBar.enablePresentLevelBorder3(true);
            this.menuBar.enablePresentLevelBorder4(false);
        }else if(this.levelNum == 4){
            this.menuBar.enablePresentLevelBorder1(false);
            this.menuBar.enablePresentLevelBorder2(false);
            this.menuBar.enablePresentLevelBorder3(false);
            this.menuBar.enablePresentLevelBorder4(true);
        }else{
            this.menuBar.enablePresentLevelBorder1(false);
            this.menuBar.enablePresentLevelBorder2(false);
            this.menuBar.enablePresentLevelBorder3(false);
            this.menuBar.enablePresentLevelBorder4(false);
        }
    }

    updateSuper = (delta) => {
        this.timer.update();
        this.mascot.update(delta);

        let i = 0;
        while (i < this.lerpingObjs.length) {
            this.lerpingObjs[i].doLerp(delta);
            i += 1;
        }

        i = 0;
        while (i < this.blinkingObjs.length) {
            this.blinkingObjs[i].doBlinking(delta);
            i += 1;
        }

        this.cleaupAudioEnded();
    }

    playAudioPath = (path) => {
        for(let i=0; i<this.audioPlayers.length; ++i){
            if(this.audioPlayers[i].soundfile == path)
            {
                return;
            }
        }

        if(this.isAnyAudioPlaying()){
            this.stopAllAudio();
        }
        
        let audio = new SAudio(this,this.listener);
        this.audioPlayers.push(audio);
        audio.play(path);
    }

    stopAllAudio = () => {        
        for (let i = 0; i < this.audioPlayers.length; ++i) {
            this.audioPlayers[i].pause(true);
        }

        while(this.audioPlayers.length > 0) {
            let audio = this.audioPlayers[0];
            this.audioPlayers.splice(0, 1);
            audio.dispose();
        }
    }


    pause = (value) => {
        this.clock.getDelta();
        this.timer.pause(value);

        for (let i = 0; i < this.audioPlayers.length; ++i) {
            this.audioPlayers[i].pause(value);
        }

        // Call pause for each dialog
        if(this.skipActionDialog)
            this.skipActionDialog.pause(value);            
        if(this.hintCompleteDialog)
            this.hintCompleteDialog.pause(value);            
        if(this.homeActionDialog)
            this.homeActionDialog.pause(value);
        if(this.endDialog)
            this.endDialog.pause(value);
    }

    onHomeActionDialogPickNewGame = () => {
        this.homeActionDialog.dispose();
        this.homeActionDialog = null;

        this.sdk.saveDataOnExit();
    }

    onHomeActionDialogContinueGame = () => {
        this.homeActionDialog.dispose();
        this.homeActionDialog = null;
        
        this.setEnableEvents(true);
        if (this.dragControls) {
            this.dragControls.dispose();
            this.dragControls = null;
        }
        //this variable is to go to 
        this.skipContinue = true;
        this.goToCheckPoint();
    }

    showHintCompleteDialog = () => {
        this.hintClicked = false;
        this.hintClickedStep = -1;
        this.setEnableEvents(false);
        this.stopAllAudio();
        this.clearPlayArea();
        this.hintCompleteDialog = new HintCompleteDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    onHintCompleteDialogPlayAgain = () => {
        this.hintCompleteDialog.dispose();
        this.hintCompleteDialog = null;

        this.stopAll();
        this.step = 0;
        this.goToNextStep();

        this.setEnableEvents(true);
    }

    onHintCompleteDialogPlayGame = () => {
        this.hintCompleteDialog.dispose();
        this.hintCompleteDialog = null;

        if(this.checkpoint == -1){
            this.goToGameplay();
        }else{
            this.goToCheckPoint();
        }
        if(this.levelNum != 0){
            this.menuBar.enableBack(true);
        }
        this.menuBar.enableHint(true);
        if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 0){
            this.menuBar.enableSkip(false);
        }
        
        if(this.sdk.getPresentationCompleteForLevel(0) == 0){
            this.menuBar.enableSkip(false);
        }
        this.setEnableEvents(true);
    }

    onSkipAction = () => {
        this.showSkipActionDialog();
    }

    showSkipActionDialog = () => {
        this.setEnableEvents(false);
        this.stopAllAudio();
        if (this.hand) this.scene.remove(this.hand.texture);
        this.clearPlayArea();
        this.timer.clearAll();
        this.checkpoint = this.step;
        if (this.dragControls) {
            this.dragControls.dispose();
            this.dragControls = null;
        }
        this.waitingForTap = false;
        this.comboTaps = "";
        if(this.levelNum == 4 && this.step > 13){
            this.homeActionDialog = new HomeActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
        }else{
            this.skipActionDialog = new SkipActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
        }
    }

    onSkipActionDialogExit = () => {
        this.skipActionDialog.dispose();
        this.skipActionDialog = null;
        
        this.setEnableEvents(true);
        if(localStorage.getItem("presentStep")){
            this.presentStep = localStorage.getItem("presentStep");
            localStorage.setItem("skipClickedAfterPrevious",this.presentStep);
            localStorage.removeItem('presentStep');
        }
        if(localStorage.getItem("stepOnHintclicked")){
            this.stepOnhint = localStorage.getItem("stepOnHintclicked");
            if(((this.levelNum == 0 && this.stepOnhint > 9) || (this.levelNum == 1 || this.levelNum == 2) && this.stepOnhint > 10) ||(this.levelNum == 3 && this.stepOnhint > 11)|| (this.levelNum == 4 && this.stepOnhint > 13)){
                if(this.levelNum != 0){
                    this.menuBar.enableBack(true);
                }
                this.menuBar.enableHint(true);
                if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 0 || (this.levelNum == 0 && this.sdk.getPresentationCompleteForLevel(this.levelNum) == 0)){
                    this.menuBar.enableSkip(false);
                }else{
                    this.menuBar.enableSkip(true);
                }
            }
            this.checkpoint = localStorage.getItem("stepOnHintclicked");
            localStorage.removeItem("stepOnHintclicked")
            this.goToCheckPoint();
        }else{
            if(this.levelNum == 4){
                this.playNewGame();
            }else{
                this.callback.playNextGame();
            };
        }

    }

    onSkipActionDialogContinue = () => {
        this.skipActionDialog.dispose();
        this.skipActionDialog = null;

        this.setEnableEvents(true);
        this.skipContinue = true;
        this.goToCheckPoint();
    }

    onBackAction = () => {
        this.showBackActionDialog();
    }

    showBackActionDialog = () => {
        this.setEnableEvents(false);
        this.stopAllAudio();
        this.clearPlayArea();
        this.timer.clearAll();
        this.checkpoint = this.step;
        this.comboTaps = "";
        this.backActionDialog = new BackActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
        this.stopAll();
    }

    onBackActionDialogPreviousLevel = () => {
        this.backActionDialog.dispose();
        this.backActionDialog = null;
        
        this.setEnableEvents(true);
        localStorage.setItem("presentStep",this.step);
        this.callback.playPreviousGame();
    }

    onBackActionDialogContinue = () => {
        this.backActionDialog.dispose();
        this.backActionDialog = null;

        this.setEnableEvents(true);

        this.goToCheckPoint();
    }

    lerpEnded = (texture) => {
        const index = this.lerpingObjs.indexOf(texture);
        if (index > -1) {
            this.lerpingObjs.splice(index, 1);
        }
    }

    blinkingEnded = (texture) => {
        const index = this.blinkingObjs.indexOf(texture);
        if (index > -1) {
            this.blinkingObjs.splice(index, 1);
        }
    }

    goToNextStep = () => {
        this.endOfStep(this.step);
        this.step = this.step + 1;
        this.goToStep(this.step);
    }

    endOfStep = (step) => {
    }

    goToStep = (step) => {
        console.log("goToStep: " + step);
        this.stepFunctions[step]();
    }

    playBackgroundMusic = (loop, soundfile) => {        
        this.audioLoader.load(soundfile, (buffer) => {
            if(this.backgroundSound.isPlaying)
                this.backgroundSound.stop();
            this.backgroundSound.setBuffer(buffer);
            this.backgroundSound.setLoop(loop);
            let vol = localStorage.getItem("volume");
            this.backgroundSound.setVolume(parseFloat(vol/2));
            this.backgroundSound.play();
        });
    }

    stopBackgroundMusic = () => {        
        if(this.backgroundSound.isPlaying)
            this.backgroundSound.stop();
    }

    onTimeOut = (ref) => {
        switch (ref) {
            case "nextstep":
                this.goToNextStep();
                break;
            case "next_confetti":
                this.showConfetti();
                break;
        }
    }

    showConfetti = () => {

        let rect = new party.Rect(window.innerWidth / 2, window.innerHeight, 3, 4);
        party.settings.gravity = 300;
        party.confetti(rect, {
            count: 100,
            countVariation: 1,
            angleSpan: 10,
            velocity: 50,
            rotationVelocityLimit: 0,
            scaleVariation: 4.8,
            size: 1.5,
            spread: 20,
            shapes: ["star", "roundedSquare", "square", "rectangle", "circle", "roundedRectangle"],
        });

    }
}

export default Level