    showPlayagainAfterEachTry = () => {
        this.setEnableEvents(false);
        this.stopAllAudio();
        this.clearPlayArea();
        this.checkpoint = this.step;
        this.playAgainOnEachTryDialog = new playAgainOnEachTryDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    afterEachTryDialogPlayAgain = () => {
        this.playAgainOnEachTryDialog.dispose();
        this.playAgainOnEachTryDialog = null;
        
        this.stopAll();
        this.step = 9;
        this.goToNextStep();
        this.checkpoint = 10;
        this.setEnableEvents(true);
    }

    afterEachTryDialogContinue = () => {
        this.playAgainOnEachTryDialog.dispose();
        this.playAgainOnEachTryDialog = null;
        
        this.setEnableEvents(true);  

        this.clearPlayArea();

        this.goToNextStep();
    }

    goToCheckPoint = () => {

        // Get the nearest checkpoint
        if (this.checkpoint < this.stepFunctions.length - 1) {
            for (let i = 0; i < this.checkpoints.length; ++i) {
                if (this.checkpoint < this.checkpoints[i]) {
                    if (i - 1 <= 0)
                        this.step = -1;
                    else
                        this.step = this.checkpoints[i - 1] - 1;
                    break;
                }
            }
        }

        this.checkpoint = -1;
        this.clearPlayArea();
        if(this.colorName) this.colorName = "white";
        this.goToNextStep();
    }

    validateForCompletion = () => {
        console.log("validateForCompletion: this.result: " + this.results);

        const allEqual = arr => arr.every(val => val === arr[0]);
        const elements = allEqual(this.results);

        if (!elements) {
            this.clearPlayArea();
            this.step -= 9;
            
            this.checkpoint = this.step;
            this.goToCheckPoint();
        } else {
            this.goToNextStep();
        }
    }

    checkForCheckPointAndContinue = () => {
        this.clearPlayArea();
        if (localStorage.getItem('stepOnHintclicked')) {
            this.showHintCompleteDialog();
        }
        else
        {
            this.goToNextStep();
        }       
    }

    level75Completed = () => {
        let progress = 0
        if (localStorage.getItem("level4progress")) {
            progress = parseFloat(localStorage.getItem("level4progress"));
        }

        if (progress < 0.75) {
            progress = 0.75;
            localStorage.setItem("level4progress", progress);
        }
        this.menuBar.level4ProgressBar.setProgress(progress);

        this.timer.addTimeOut(1.0, "nextstep");
    }

    level100Completed = () => {
        let progress = 0
        if (localStorage.getItem("level4progress")) {
            progress = parseFloat(localStorage.getItem("level4progress"));
        }

        progress = 1.0;
        localStorage.setItem("level4progress", progress);
        this.menuBar.level4ProgressBar.setProgress(progress);

        this.timer.addTimeOut(1.0, "nextstep");
    }

    clearPlayArea = () => {
        this.mascot.disable();
        if (this.menuBar) this.menuBar.enableReplayHand(false);
        this.removeAllShapes();

        if (this.tick) this.scene.remove(this.tick.texture);
        if (this.hand) this.scene.remove(this.hand.texture);
    }

    clearPlayAreaAndNext = () => {
        this.clearPlayArea();

        this.goToNextStep();
    }

    getColorCode = (compareColor) => {
        this.paintColor = "white";
        switch(compareColor){
            case "paintBrown":
                this.paintColor = this.brownColor;
                break;
            case "paintPink":
                this.paintColor = this.pinkColor;
                break;
            case "paintBlack":
                this.paintColor = "black";
                break;
            case "paintOrange":
                this.paintColor = this.orangeColor;
                break;
            case "paintBlue":
                this.paintColor = this.blueColor;
                break;
            case "paintRed":
                this.paintColor = this.redColor;
                break;
            case "paintPurple":
                this.paintColor = this.purpleColor;
                break;
            case "paintYellow":
                this.paintColor = this.yellowColor;
                break;
            case "paintGrey":
                this.paintColor = this.greyColor;
                break;
            case "paintGreen":
                this.paintColor = this.greenColor;
                break;
        }
        return this.paintColor;
    }

