    allClickedComboShapes = (twoCorrectObjs,twoInCorrectObjs, correctAudio, inCorrectAudio) =>{
        const thisObj = this;
        thisObj.correctCount = [];
        twoCorrectObjs.forEach(function (item) {
            thisObj.colorInHex = item.texture.material.color.getHexString();
            if(item && thisObj.colorInHex !== 'ffffff'){
                thisObj.correctCount.push(1);
                thisObj.sdk.setCorrectAttempts(thisObj.levelNum);
                thisObj.results.push(1);
            }
        });
        twoInCorrectObjs.forEach(function (item) {
            thisObj.colorInHex = item.texture.material.color.getHexString();
            if(item && thisObj.colorInHex !== 'ffffff'){
                setTimeout(() => {
                    item.texture.material.color.set("white");
                }, 2000);
                thisObj.correctCount.push(2);
                thisObj.sdk.setWrongAttempts(thisObj.levelNum);
                thisObj.results.push(0);
            }
        });
        const allEqual = arr => arr.every(val => val === 1);
        const elements = allEqual(thisObj.correctCount);
        if(elements && twoCorrectObjs.length == thisObj.correctCount.length ){
            if(thisObj.colorName) thisObj.colorName = 'white';
            thisObj.playAudioPath('./audio/common/For_correct_answer.mp3');
            thisObj.waitingForTap = false;
            thisObj.comboTaps = "";
            thisObj.playAudioPath(correctAudio);
            thisObj.timer.addTimeOut(3, "nextstep");
        }else{
            thisObj.playAudioPath('./audio/common/For_the_wrong_answer.mp3');
            thisObj.playAudioPath(inCorrectAudio);
        }
        console.log(thisObj.results);
    }

    voiceOutSelection = () => {
        switch (this.comboTaps) {
            case "obj7andobj10": case "obj10andobj4": case "obj12andobj14": case "obj4andobj12":
                this.playAudioPaths(1, false, 2, './audio/level4/NS_A_L4_5.mp3');
                break;
            case "obj11andobj15": case "obj15andobj13": case "obj8andobj9":
                this.playAudioPaths(1, false, 2, './audio/level4/NS_A_L4_7.mp3');
                break;
            case "obj9andobj2": case "obj13andobj11": case "obj1andobj10": case "obj8andobj11":
                this.playAudioPaths(1, false, 2, './audio/level4/NS_A_L4_9.mp3');
                break;
            case "obj3andobj1": case "obj5andobj1": case "obj14andobj4": case "obj5andobj3":
                this.playAudioPaths(1, false, 2, './audio/level4/NS_A_L4_3.mp3');
                break;
        }
    }

    playAudioPaths = (mascotPos, enableMascot, duration, path) => {
        if (enableMascot)
        {
            this.mascot.enable(0, mascotPos);
            this.mascot.enableTalkingAnimation(duration);
        }else{
            this.mascot.disable();
        }
        this.currentAudioPath = path;
        this.playAudioPath( this.currentAudioPath);
    }


    enableTrialCriteria = () => {
        this.considerPoints = false;
        this.results = [];
        this.goToNextStep();
    }

    enableCompletionCriteria = () => {
        console.log("enableCompletionCriteria");
        this.considerPoints = true;
        this.results = [];
        this.goToNextStep();
    }

