import * as THREE from 'three'
import LevelMenu from './Levelmenu'
import Level0 from './Level0'
import Level1 from './Level1'
import Level2 from './Level2'
import Level3 from './Level3'
import Level4 from './Level4'
import LoadingManager from './LoadingManager';
import Config from './Config.js'
import SDK from './sdk/SDK';

var hidden, visibilityChange;
if (typeof document.hidden !== "undefined") { // Opera 12.10 and Firefox 18 and later support
    hidden = "hidden";
    visibilityChange = "visibilitychange";
} else if (typeof document.msHidden !== "undefined") {
    hidden = "msHidden";
    visibilityChange = "msvisibilitychange";
} else if (typeof document.webkitHidden !== "undefined") {
    hidden = "webkitHidden";
    visibilityChange = "webkitvisibilitychange";
}

class Game {
    constructor(container) {
        window.addEventListener('resize', () => {
            this.updateScale();
        });

        var vol = localStorage.getItem("volume");
        if (vol == null)
            localStorage.setItem("volume", "1");

        // Scene
        this.scene = new THREE.Scene()

        this.loadingManager = new LoadingManager(this.scene);

        // Camera
        var aspect = window.innerWidth / window.innerHeight;
        this.camera = new THREE.OrthographicCamera(Config.frustumSize * aspect / - 2, Config.frustumSize * aspect / 2, Config.frustumSize / 2, - Config.frustumSize / 2, 0.1, 400)
        this.camera.position.z = 1
        this.scene.add(this.camera)

        // Renderer
        this.renderer = new THREE.WebGLRenderer({ antialias: true });
        this.renderer.setPixelRatio(window.devicePixelRatio);
        container.appendChild(this.renderer.domElement);

        // create an AudioListener and add it to the camera
        this.listener = new THREE.AudioListener();
        this.camera.add(this.listener );

        // create a global audio source
        this.sound = new THREE.Audio(this.listener );
        this.sfxSound = new THREE.Audio(this.listener );
        this.backgroundSound = new THREE.Audio(this.listener );

        this.sdk = new SDK();

        this.levelNum = -1;
        this.pause = false;
        
        // Load Level
        this.loadLevel();

        this.updateScale();

        localStorage.removeItem('presentStep');
        localStorage.removeItem('skipClickedAfterPrevious');
        localStorage.removeItem('stepOnHintclicked');

        document.addEventListener(visibilityChange, this.handleVisibilityChange, false);
        
        if (Config.debug) {
            // window.addEventListener('dblclick', () => {
            //     const fullscreenElement = document.fullscreenElement || document.webkitFullscreenElement

            //     if (!fullscreenElement) {
            //         if (this.renderer.domElement.requestFullscreen) {
            //             this.renderer.domElement.requestFullscreen()
            //         }
            //         else if (this.renderer.domElement.webkitRequestFullscreen) {
            //             this.renderer.domElement.webkitRequestFullscreen()
            //         }
            //     }
            //     else {
            //         if (document.exitFullscreen) {
            //             document.exitFullscreen()
            //         }
            //         else if (document.webkitExitFullscreen) {
            //             document.webkitExitFullscreen()
            //         }
            //     }
            // })
        }

        // Animate
        const clock = new THREE.Clock()
        const tick = () => {
            if (this.pause == false) {
                // Update objects
                this.level.update();
            }
            // Render
            this.renderer.render(this.scene, this.camera)

            // Call tick again on the next frame
            requestAnimationFrame(tick)

            // if (this.pause && document.hasFocus()) {
            //     this.focusin();
            // }
            // else if(!this.pause && !document.hasFocus()){
            //     this.focusout();
            // }
        }

        tick()
    }

    dispose() {

    }

    focusin = () => {
        this.pause = false;
        this.sound.setVolume(1.0);
        this.backgroundSound.play();
        this.level.pause(false);    
}

    focusout = () => {
        this.pause = true;
        this.sound.setVolume(0.0);
        this.backgroundSound.pause();
        this.level.pause(true);
    }

    handleVisibilityChange = () => {
        if (document[hidden] && !this.pause) {
            this.focusout();
        } else if(this.pause){
            this.focusin();  
        }
    }

    loadLevel = () => {
        switch (this.levelNum) {
            case -1:
                this.level = new LevelMenu(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound,this.listener ,this.sdk, this);
                break;
            case 0:
                this.level = new Level0(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound,this.listener , this.sdk, this);
                break;
            case 1:
                this.level = new Level1(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound, this.listener , this.sdk, this);
                break;
            case 2:
                this.level = new Level2(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound, this.listener , this.sdk, this);
                break;
            case 3:
                this.level = new Level3(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound, this.listener , this.sdk, this);
                break;
            case 4:
                this.level = new Level4(this.loadingManager.obj, this.scene, this.camera, this.renderer, this.sound, this.backgroundSound, this.listener , this.sdk, this);
                break;
        }
    }

    playNextGame = () => {
        this.level.dispose();

        switch (this.levelNum) {
            case -1:
                this.levelNum = 0;
                break;
            case 0:
                this.levelNum = 1;
                break;
            case 1:
                this.levelNum = 2;
                break;
            case 2:
                this.levelNum = 3;
                break;
            case 3:
                this.levelNum = 4;
                break;
        }

        this.loadLevel();
    }

    playPreviousGame = () => {
        this.level.dispose();
        switch (this.levelNum) {
            case 0:
                this.levelNum = 0;
                break;
            case 1:
                this.levelNum = 0;
                break;
            case 2:
                this.levelNum = 1;
                break;
            case 3:
                this.levelNum = 2;
                break;
            case 4:
                this.levelNum = 3;
                break;
        }

        this.loadLevel();
    }

    updateScale = () => {
        var aspect = window.innerWidth / window.innerHeight;

        this.camera.left = Config.frustumSize * aspect / - 2;
        this.camera.right = Config.frustumSize * aspect / 2;
        this.camera.top = Config.frustumSize / 2;
        this.camera.bottom = - Config.frustumSize / 2;

        this.camera.updateProjectionMatrix();
        this.renderer.setSize(window.innerWidth, window.innerHeight);

        if (this.level)
            this.level.updateForResize();
    }

}

export default Game