import * as THREE from 'three'

let audioNum = 0;

class SAudio {
    constructor(callback, listener) {
        this.sound = new THREE.Audio(listener);
        this.audioLoader = new THREE.AudioLoader();
        this.uuid = audioNum;
        audioNum += 1;
        this.callback = callback;
        this.isPlaybackStarted = false;
        this.audio = null;
        this.soundfile = "";
    }

    dispose() {
        this.stop();
    }

    isAudioPlaying = () => {
        // if (this.audio != null) {
        //     return !this.audio.ended;
        // }
        if(!this.isPlaybackStarted)
            return true;

        return this.sound.isPlaying; //this.sound.isPlaying;
    }

    play = (soundfile) => {
        this.soundfile = soundfile;
        // console.log(soundfile);
        // try{
        //     this.audio = new Audio(soundfile);
        //     this.audio.muted = true;
        //     this.audio.play();
        //     this.audio.muted = false;
        //     this.audio.play();
        //     this.isPlaying = true;
        // }catch(e){
        //     this.audio = null;
        //     console.log("audio play exception catched");
        // }
        this.audioLoader.load(soundfile, (buffer) => {           
            this.sound.setBuffer(buffer);
            this.sound.setLoop(false);
            this.sound.setVolume(1.0);
            this.sound.play();

            this.isPlaybackStarted = true;
        });
    }

    pause = (val) => {
        console.log("pause audio called");
        //if (this.isPlaying) {
            if (val)
                this.sound.pause();
            else
                this.sound.play();
        //}
    }

    stop = () => {
        if(this.sound.isPlaying)
            this.sound.stop();

        // if (this.isPlaying)
        //     this.audio.pause();
    }
}

export default SAudio