import * as THREE from 'three'
import Dialog from './Dialog'

class SkipActionDialog extends Dialog
{
    constructor(loadingManager, scene, camera, renderer, sound,listner, callback){
        var imagePaths = [
            './textures/common/interactions/Exit.png',
            './textures/common/interactions/continue_green.png',
            ];
        super (loadingManager, scene, camera, renderer, sound,listner, callback, imagePaths)     

        this.levelNum = callback.levelNum;
        this.levelStep = callback.step;

        if(((this.levelNum == 1 || this.levelNum == 2) && this.levelStep > 10)||(this.levelNum == 3 && this.levelStep > 11)){
            if(localStorage.getItem("presentStep")){
                this.playAudioPath( './audio/common/GL_A_8.mp3')
            }else{
                this.playAudioPath( './audio/common/GL_A_17.mp3')
            }
        }else if(this.levelNum == 4 && this.levelStep > 13){
            this.playAudioPath( './audio/common/GL_A_18.mp3')
        }
        else{
            this.playAudioPath( './audio/common/GL_A_7.mp3')
        }
        
    }

    dispose(){
        if(this.confetti) this.scene.remove(this.confetti.object);        

        document.removeEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.removeEventListener( 'touchdown', this.onDocumentMouseDown, false );
        document.removeEventListener( 'mouseup', this.onDocumentMouseUp, false );
        document.removeEventListener( 'touchup', this.onDocumentMouseUp, false );

        this.disposeSuper();
    }

    updateForResize = () =>{
        this.updateForResizeSuper();
    }

    createScene = () =>{
         //adding a transparent background with black color
        const geometry = new THREE.PlaneGeometry( window.innerWidth, window.innerHeight );
        const material = new THREE.MeshBasicMaterial({ 
            opacity: 0.50, 
            color:0xff000000,
            transparent: true,
            side: THREE.DoubleSide, 
            depthWrite: false
        });
        this.transparentPlane = new THREE.Mesh( geometry, material );
        this.transparentPlane.scale.set( window.innerWidth, window.innerHeight, 1 );
        this.scene.add( this.transparentPlane );

        this.button1 = this.getTexture2(this.assetManager.textures[0], "Exit", true, 0, 100, 0, 0, 0.2);
        this.scene.add(this.button1.texture)
        this.button1.setScale(0.8 , 0.8);

        this.button2 = this.getTexture2(this.assetManager.textures[1], "Continue", true, 0, -100, 0, 0, 0.2);
        this.scene.add(this.button2.texture)
        this.button2.setScale(0.8 , 0.8);
        
        setTimeout(() => {
            this.enableEvents = true;
        }, 500);

        this.updateForResize();        

        document.addEventListener( 'mousedown', this.onDocumentMouseDown, false );
        document.addEventListener( 'touchdown', this.onDocumentMouseDown, false );
        document.addEventListener( 'mouseup', this.onDocumentMouseUp, false );
        document.addEventListener( 'touchup', this.onDocumentMouseUp, false );
    }     
   
    update = () => {
    }

    onDocumentMouseDown = ( event ) => {
        this.mouse.x = ( event.clientX / this.renderer.domElement.clientWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / this.renderer.domElement.clientHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.mouse, this.camera );

        var intersects = this.raycaster.intersectObjects( this.scene.children );

        if ( intersects.length > 0 )
        {
        }
    }
    
    onDocumentMouseUp = ( event ) => {
        if (!this.enableEvents)
            return;

        this.mouse.x = ( event.clientX / this.renderer.domElement.clientWidth ) * 2 - 1;
        this.mouse.y = - ( event.clientY / this.renderer.domElement.clientHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.mouse, this.camera );

        var intersects = this.raycaster.intersectObjects( this.scene.children );

        if ( intersects.length > 0 )
        {
            switch(intersects[0].object.name){
                case "Exit":
                    this.scene.remove(this.transparentPlane);
                    this.callback.onSkipActionDialogExit();
                    break;
                case "Continue":
                    this.scene.remove(this.transparentPlane);
                    this.callback.onSkipActionDialogContinue();
                    break;
            }
        }
    }
}

export default SkipActionDialog