import * as THREE from 'three'
import AssetManager from './AssetManager'
import Texture from './Texture'
import Config from './Config'
import Utils from './Utils'
import MenuBar from './MenuBar'
import Timer from './Timer'
import Mascot from './Mascot'
import { DragControls } from 'three/examples/jsm/controls/DragControls'
import Level0Dialog1 from './Level0Dialog1'
import MenuDialog from './MenuDialog'
import Level from './Level'
import HomeActionDialog from './HomeActionDialog'

class LevelMenu extends Level {
    constructor(loadingManager, scene, camera, renderer, sound, backgroundSound, listner, sdk, callback) {
        var imagePaths = [
            './textures/common/background/bg.png',
            './textures/common/interactions/Hand_Cursor3.png',
            './textures/level_0/Frame_circle.png',
            './textures/level_0/Frame_rectangle.png',
            './textures/level_0/Frame_square.png',
            './textures/level_0/Frame_triangle.png',
            './textures/level_0/Circle.png',
            './textures/level_0/Rectangle.png',
            './textures/level_0/Square.png',
            './textures/level_0/Triangle.png',
            './textures/common/interactions/highlight.png',
            './textures/common/background/02.png',
            './textures/common/background/03.png',
            './textures/common/background/04.png',
        ];
        super(loadingManager, scene, camera, renderer, sound, backgroundSound, listner, imagePaths, false, sdk);

        this.callback = callback;
        this.positionalObjs = [];
        this.levelNum = 0;

        this.menuDialog = new MenuDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,listner, this);
        this.setEnableEvents(false);
    }

    dispose() {
        if (this.menuDialog) this.menuDialog.dispose();

        this.disposeSuper();
    }

    updateForResize = () => {
        this.updateForResizeSuper();

        for (let i = 0; i < this.positionalObjs.length; ++i) {
            this.positionalObjs[i].updateForResize();
        }
    }

    createScene = () => {
        // Background
        let backgroundImage = this.getTexture2(this.assetManager.textures[0], "background", true, 0, 0, 0, 0, -100, new THREE.Vector2(3, 1), THREE.RepeatWrapping);
        this.scene.add(backgroundImage.texture);

        let leftTree = this.getTexture2(this.assetManager.textures[11], "lefttree", true, -1, 155, 0, 0, -100);
        leftTree.setScale(0.84, 0.84);
        this.scene.add(leftTree.texture);
        this.positionalObjs.push(leftTree);

        let rightTree = this.getTexture2(this.assetManager.textures[12], "righttree", true, 1, -133, 0, 0, -100);
        rightTree.setScale(0.84, 0.84);
        this.scene.add(rightTree.texture);
        this.positionalObjs.push(rightTree);

        let bottomLand = this.getTexture2(this.assetManager.textures[13], "bottomland", true, 0, 0, -1, 22, -100, new THREE.Vector2(3, 1));
        this.scene.add(bottomLand.texture);
        this.positionalObjs.push(bottomLand);

        this.updateForResize();
    }

    update = () => {
        const delta = this.clock.getDelta();
        this.updateSuper(delta);
    }

    onMenuBarLoaded = () => {
        this.menuBar.enableHome(true);
        this.menuBar.enableBack(false);
        this.menuBar.enableHint(false);
        this.menuBar.enableReward(true);
        this.menuBar.enableAudioPlayButton(true);
        this.menuBar.enableSkip(this.sdk.getCurrentLevel() > 0);

        this.onMenuBarLoadedSuper();
    }

    onHintAction = () => {
    }

    onHomeAction = () => {
    }

    onAudioReplayAction = () => {
    }

    stopAll = () => {
    }

    setupSteps = () => {
    }

    playAgain = () => {
    }

    playGame = () => {
    }

    playNewGame = () => {
        this.callback.playNextGame();
    }
}

export default LevelMenu