import * as THREE from 'three'
import { Vector3 } from 'three';
import Utils from './Utils'

class Texture {
    constructor(texture, xref, xdisp, yref, ydisp) {
        this.texture = texture;
        this.xref = xref;
        this.xdisp = xdisp;
        this.yref = yref;
        this.ydisp = ydisp;
    }

    updateForResize = () => {
        switch (this.xref) {
            case 0: // center
                this.texture.position.x = this.xdisp;
                break;
            case -1: // left
                this.texture.position.x = Utils.getLeftBorder() + this.xdisp;
                break;
            case 1: // right
                this.texture.position.x = Utils.getRightBorder() + this.xdisp;
                break;
        }
        switch (this.yref) {
            case 0: // center
                this.texture.position.y = this.ydisp;
                break;
            case 1: // top
                this.texture.position.y = Utils.getTopBorder() + this.ydisp;
                break;
            case -1: // bottom
                this.texture.position.y = Utils.getBottomBorder() + this.ydisp;
                break;
        }
    }

    getWidth = () => {
        return this.texture.geometry.parameters.width;
    }

    getHeight = () => {
        return this.texture.geometry.parameters.height;
    }

    setNewPosition = (xref, xdisp, yref, ydisp) => {
        this.xref = xref;
        this.xdisp = xdisp;
        this.yref = yref;
        this.ydisp = ydisp;
        this.updateForResize();
    }

    setScale = (x, y) => {
        this.texture.scale.x = x;
        this.texture.scale.y = y;
    }

    setBlinking = (duration, callback = null) => {
        this.time = 0;
        this.totalBlinkTime = 0;
        this.blinkDuration = duration;
        this.blinking = true;
        this.callback = callback;
    }

    endBlinking = () => {
        if (this.blinking) {
            this.texture.material.opacity = 1.0;
        }
        this.blinking = false;

        if (this.callback) {
            this.callback.blinkingEnded(this);
        }
    }

    doBlinking = (delta) => {
        if (this.blinking) {
            this.time += delta;
            this.totalBlinkTime += delta;
            this.texture.material.opacity = this.time / 0.1;
            if (this.time >= 0.1) {
                this.time = 0;
            }
            if (this.blinkDuration != -1) {
                if (this.totalBlinkTime >= this.blinkDuration)
                    this.endBlinking();
            }
        }
    }

    setLerp = (startPos, endPos, duration, loop, cycle, callback = null, totalDuration = 100) => {
        this.startPos = startPos.clone();
        this.endPos = endPos.clone();
        this.startPosCopy = startPos.clone();
        this.endPosCopy = endPos.clone();
        this.duration = duration;
        this.loop = loop;
        this.cycle = cycle;
        this.time = 0;
        this.lerping = true;
        this.callback = callback;
        this.totalTime = 0;
        this.totalLerpDuration = totalDuration;
    }

    endLerp = () => {
        if (this.lerping) {
            if (this.loop)
                this.texture.position.lerpVectors(this.endPos, this.startPos, 1.0);
            else
                this.texture.position.lerpVectors(this.startPos, this.endPos, 1.0);
        }
        this.lerping = false;

        if (this.callback) {
            this.callback.lerpEnded(this);
        }
    }

    doLerp = (delta) => {
        if (this.lerping) {
            this.time += delta;
            this.totalTime += delta;
            this.texture.position.lerpVectors(this.startPosCopy, this.endPosCopy, this.time / this.duration);
            if (this.time >= this.duration) {
                this.time = 0;

                if (!this.loop || this.totalTime >= this.totalLerpDuration) {
                    this.endLerp();
                }
                else {
                    if (this.cycle) {
                        this.startPosCopy = this.endPos.clone();
                        this.endPosCopy = this.startPos.clone();
                    }
                }
            }
        }
    }

    static get2DTexture = (texture, vec = null) => {
        texture.minFilter = THREE.LinearFilter;
        texture.magFilter = THREE.LinearFilter;
        if (vec) {
            if (vec.x > 1)
                texture.wrapS = THREE.MirroredRepeatWrapping;
            if (vec.y > 1)
                texture.wrapT = THREE.MirroredRepeatWrapping;

            texture.repeat = vec;
        }
        return texture;
    }

    static width(texture) {
        return texture.image.width
    }
    static height(texture) {
        return texture.image.height
    }
}

export default Texture