const debug = false;
const myapp = false;

class SDK {
	constructor() {
		this.data = null; // Used to modify and send back to app again

		this.levelDetails = { "currentLevel": { "level": 0, "presentationCompleted": 0 }, "level0": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 } }, "level1": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level2": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level3": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level4": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 } }
		this.resetLevelCompletionProgress();

		// localStorage.clear(); //uncomment if want to clear cached progress in web

		// comment below 3 line if with app else uncomment
		if (debug) {
			this.message = { "data": {} };
			this.message.data = { "learningTrackid": 1, "gameId": 1, "gameVersion": "string", "predGameId": 0, "gamePath": "https://kreedo-game-upload-poc.s3.us-east-2.amazonaws.com/701_LearningTeens.zip", "isActive": true, "isblocked": false, "isGameDownloadComplete": true, "gameName": "Place Value Quantities", "attemptId": 0, "totalRewards": 0, "completedCount": 0, "startDateTime": "", "endDateTime": "", "completed": 0, "isMusic": true, "levelDetails": { "currentLevel": { "level": 0, "presentationCompleted": 0 }, "level0": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 } }, "level1": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level2": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level3": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 }, "level4": { "presentation": { "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0 }, "completed": 0, "playCount": 0, "completedCount": 0, "timeSpent": 0, "correctAttempts": 0, "incorrectAttempts": 0 } } };
			this.data = this.message.data;
		}

		//app will trigger this event on load finish.
		// uncomment this listener if with app else comment
		document.addEventListener("message", this.handleEvent);
	}

	handleEvent = (messageTemp) => {
		this.data = myapp ? JSON.parse(messageTemp.data) : messageTemp.data;
		this.resetLevelCompletionProgress();
	}

	resetLevelCompletionProgress = () => {
		if (this.data && this.data.levelDetails.level4.completed) {
			localStorage.setItem("level1progress", 1.0);
			localStorage.setItem("level2progress", 1.0);
			localStorage.setItem("level3progress", 1.0);
			localStorage.setItem("level4progress", 1.0);
		}
		else if (this.data && this.data.levelDetails.level3.completed) {
			localStorage.setItem("level1progress", 1.0);
			localStorage.setItem("level2progress", 1.0);
			localStorage.setItem("level3progress", 1.0);
			localStorage.setItem("level4progress", 0);
		}
		else if (this.data && this.data.levelDetails.level2.completed) {
			localStorage.setItem("level1progress", 1.0);
			localStorage.setItem("level2progress", 1.0);
			localStorage.setItem("level3progress", 0);
			localStorage.setItem("level4progress", 0);
		}
		else if (this.data && this.data.levelDetails.level1.completed) {
			localStorage.setItem("level1progress", 1.0);
			localStorage.setItem("level2progress", 0);
			localStorage.setItem("level3progress", 0);
			localStorage.setItem("level4progress", 0);
		}
		else {
			localStorage.setItem("level1progress", 0);
			localStorage.setItem("level2progress", 0);
			localStorage.setItem("level3progress", 0);
			localStorage.setItem("level4progress", 0);
		}
	}

	setLevelStarted = (levelNum) => {
		switch (levelNum) {
			case 0:
				this.data.levelDetails.level0.presentation.playCount += 1;
				break;

			case 1:
				this.data.levelDetails.level1.presentation.playCount += 1;
				break;

			case 2:
				this.data.levelDetails.level2.presentation.playCount += 1;
				break;

			case 3:
				this.data.levelDetails.level3.presentation.playCount += 1;
				break;

			case 4:
				this.data.levelDetails.level4.presentation.playCount += 1;
				break;
		}
		this.sendToApp();
	}

	getCurrentLevel = () => {
		return this.data.levelDetails.currentLevel.level;
	}

	setPlaycount = (levelNum) => {
		switch (levelNum) {
			case 1:
				this.data.levelDetails.level1.playCount += 1;
				break;

			case 2:
				this.data.levelDetails.level2.playCount += 1;
				break;

			case 3:
				this.data.levelDetails.level3.playCount += 1;
				break;

			case 4:
				this.data.levelDetails.level4.playCount += 1;
				break;
		}

		this.sendToApp();
	}

	setLevelCompleted = (levelNum, timeSpent, award = false) => {
		if (award)
			this.addRewardPoints(this.getRewardPointsPerLevel());

		switch (levelNum) {
			case 0:
				if (levelNum == this.data.levelDetails.currentLevel.level) {
					this.data.levelDetails.currentLevel.level = 1;
					this.data.levelDetails.currentLevel.presentationCompleted = 0;
				}
				this.data.levelDetails.level0.presentation.completed = 1;
				this.data.levelDetails.level0.presentation.completedCount += 1;
				this.data.levelDetails.level0.presentation.timeSpent += timeSpent;
				break;

			case 1:
				if (levelNum == this.data.levelDetails.currentLevel.level) {
					this.data.levelDetails.currentLevel.level = 2;
					this.data.levelDetails.currentLevel.presentationCompleted = 0;
				}
				this.data.levelDetails.level1.completedCount += 1;
				this.data.levelDetails.level1.completed = 1
				this.data.levelDetails.level1.timeSpent += timeSpent;
				break;

			case 2:
				if (levelNum == this.data.levelDetails.currentLevel.level) {
					this.data.levelDetails.currentLevel.level = 3;
					this.data.levelDetails.currentLevel.presentationCompleted = 0;
				}
				this.data.levelDetails.level2.completedCount += 1;
				this.data.levelDetails.level2.completed = 1
				this.data.levelDetails.level2.timeSpent += timeSpent;
				break;

			case 3:
				if (levelNum == this.data.levelDetails.currentLevel.level) {
					this.data.levelDetails.currentLevel.level = 4;
					this.data.levelDetails.currentLevel.presentationCompleted = 0;
				}
				this.data.levelDetails.level3.completedCount += 1;
				this.data.levelDetails.level3.completed = 1
				this.data.levelDetails.level3.timeSpent += timeSpent;
				break;

			case 4:
				if (levelNum == this.data.levelDetails.currentLevel.level) {
					this.data.levelDetails.currentLevel.level = 1;
				}
				this.data.levelDetails.level4.completedCount += 1;
				this.data.isGameCompleted = 1;
				this.data.levelDetails.level4.completed = 1
				this.data.levelDetails.level4.timeSpent += timeSpent;
				this.data.isGameCompleted = "completed";
				break;
		}
		alert(JSON.stringify(this.data));
		this.sendToApp();
	}

	setLevelPresenationCompleted = (levelNum) => {
		if (levelNum == this.data.levelDetails.currentLevel.level)
			this.data.levelDetails.currentLevel.presentationCompleted = 1;
	}

	setPresentationCompleted = (levelNum, timeSpent) => {
		switch (levelNum) {
			case 1:
				this.data.levelDetails.level1.presentation.completedCount += 1;
				this.data.levelDetails.level1.presentation.completed = 1;
				this.data.levelDetails.level1.presentation.timeSpent = parseInt(this.data.levelDetails.level1.presentation.timeSpent) +timeSpent;
				break;
			case 2:
				this.data.levelDetails.level2.presentation.completedCount += 1;
				this.data.levelDetails.level2.presentation.completed = 1;
				this.data.levelDetails.level2.presentation.timeSpent =parseInt(this.data.levelDetails.level2.presentation.timeSpent) +timeSpent;
				break;
			case 3:
				this.data.levelDetails.level3.presentation.completedCount += 1;
				this.data.levelDetails.level3.presentation.completed = 1;
				this.data.levelDetails.level3.presentation.timeSpent =parseInt(this.data.levelDetails.level3.presentation.timeSpent)+ timeSpent;
				break;
			case 4:
				this.data.levelDetails.level4.presentation.completedCount += 1;
				this.data.levelDetails.level4.presentation.completed = 1;
				this.data.levelDetails.level4.presentation.timeSpent =parseInt(this.data.levelDetails.level4.presentation.timeSpent)+ timeSpent;
				break;
		}
		this.sendToApp();
	}

	setCorrectAttempts = (levelNum) => {
		switch (levelNum) {
			case 1:
				this.data.levelDetails.level1.correctAttempts += 1;
				break;
			case 2:
				this.data.levelDetails.level2.correctAttempts += 1;
				break;
			case 3:
				this.data.levelDetails.level3.correctAttempts += 1;
				break;
			case 4:
				this.data.levelDetails.level4.correctAttempts += 1;
				break;
		}
		this.sendToApp();
	}

	setWrongAttempts = (levelNum) => {
		switch (levelNum) {
			case 1:
				this.data.levelDetails.level1.incorrectAttempts += 1;
				break;
			case 2:
				this.data.levelDetails.level2.incorrectAttempts += 1;
				break;
			case 3:
				this.data.levelDetails.level3.incorrectAttempts += 1;
				break;
			case 4:
				this.data.levelDetails.level4.incorrectAttempts += 1;
				break;
		}
		this.sendToApp();
	}

	removeCorrectAttempts = (levelNum) =>{
		switch (levelNum) {
			case 1:
				this.data.levelDetails.level1.correctAttempts -=1;
				break;
			case 2:
				this.data.levelDetails.level2.correctAttempts -=1;
				break;
			case 3:
				this.data.levelDetails.level3.correctAttempts -=1;
				break;
			case 4:
				this.data.levelDetails.level4.correctAttempts -=1;
				break;
		}
		this.sendToApp();
	}

	setMusicButtonClicked = (bValue) => {
		this.data.isMusic = bValue ? 1 : 0;

		this.sendToApp();
	}

	addRewardPoints = (rewards) => {
		this.data.totalRewards += rewards;
	}

	getPresentationCompleteForLevel = (levelnumber) => {
		if (levelnumber == 0) {
			return this.data.levelDetails.level0.presentation.completed;
		} else if (levelnumber == 1) {
			return this.data.levelDetails.level1.presentation.completed;
		} else if (levelnumber == 2) {
			return this.data.levelDetails.level2.presentation.completed;
		} else if (levelnumber == 3) {
			return this.data.levelDetails.level3.presentation.completed;
		} else if (levelnumber == 4) {
			return this.data.levelDetails.level4.presentation.completed;
		}
	}

	getIfLevelIsCompleted = (levelnumber) => {
		if (levelnumber == 0) {
			return this.data.levelDetails.level0.presentation.completed;
		} else if (levelnumber == 1) {
			return this.data.levelDetails.level1.completed;
		} else if (levelnumber == 2) {
			return this.data.levelDetails.level2.completed;
		} else if (levelnumber == 3) {
			return this.data.levelDetails.level3.completed;
		} else if (levelnumber == 4) {
			return this.data.levelDetails.level4.completed;
		}
	}

	getPresentationTimespent = (levelnumber) => {
		if (levelnumber == 0) {
			return this.data.levelDetails.level0.presentation.timeSpent;
		} else if (levelnumber == 1) {
			return this.data.levelDetails.level1.presentation.timeSpent;
		} else if (levelnumber == 2) {
			return this.data.levelDetails.level2.presentation.timeSpent;
		} else if (levelnumber == 3) {
			return this.data.levelDetails.level3.presentation.timeSpent;
		} else if (levelnumber == 4) {
			return this.data.levelDetails.level4.presentation.timeSpent;
		}
	}

	getTotalRewardPoints = () => {
		return this.data.totalRewards;
	}

	getRewardPointsPerLevel = () => {
		return 10;
	}

	// sendToApp = () => {
	// 	console.log(JSON.stringify(this.data));
	// 	if (window && window.ReactNativeWebView) {
	// 		window.ReactNativeWebView.postMessage(JSON.stringify(this.data));
	// 	}
	// }	

	sendToApp = () => {
		console.log(JSON.stringify(this.data));
		try {
			JSON.parse(JSON.stringify(this.data));
			if (window && window.ReactNativeWebView) {
				window.ReactNativeWebView.postMessage(JSON.stringify(this.data));
			}
		} catch (error) {
			console.log("Send App")
			console.log(error)
		}
	};

	// Call when home -> pick a new game
	saveDataOnExit = () => {
		console.log(JSON.stringify(this.data));
		if (window && window.ReactNativeWebView) {
			window.ReactNativeWebView.postMessage(JSON.stringify({ "home": 1, "gameData": this.data }));
		}
	}
};

export default SDK;