import * as THREE from 'three'

class AssetManager 
{
    constructor(){
        this.textures = [];
        this.materials = [];
        this.gos = [];                
    }

    dispose(){
        for(let i=0; i<this.textures.length; ++i)
            this.textures[i].dispose();
        for(let i=0; i<this.materials.length; ++i)
            this.materials[i].dispose();
        for(let i=0; i<this.gos.length; ++i)
            this.gos[i].dispose();
    }

    loadTextures = async(loadingManager, paths) => {
        this.textureLoader = new THREE.TextureLoader(loadingManager)
    
        const allTexturesPromises = paths.map(pathName => {  
            return this.textureLoader.loadAsync(pathName);
        });
    
        this.textures = await Promise.all(allTexturesPromises);
    }    
}

export default AssetManager 