    checkForCheckPointAndContinue = () => {
        this.clearPlayArea();
        if (localStorage.getItem('stepOnHintclicked')) {
            this.showHintCompleteDialog();
        }
        else
        {
            this.goToNextStep();
        }       
    }

    goToCheckPoint = () => {

        // Get the nearest checkpoint
        if (this.checkpoint < this.stepFunctions.length - 1) {
            for (let i = 0; i < this.checkpoints.length; ++i) {
                if (this.checkpoint < this.checkpoints[i]) {
                    if (i - 1 <= 0)
                        this.step = -1;
                    else
                        this.step = this.checkpoints[i - 1] - 1;
                    break;
                }
            }
        }

        this.checkpoint = -1;
        if(this.checkIfClicked || this.shapeResults){
            this.checkIfClicked = [];
            this.shapeResults = [];
        }
        this.clearPlayArea();
        this.goToNextStep();
    }

    showPlayagainAfterEachTry = () => {
        this.setEnableEvents(false);
        this.stopAllAudio();
        this.clearPlayArea();
        this.checkpoint = this.step;
        this.playAgainOnEachTryDialog = new playAgainOnEachTryDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound,this.listener, this);
    }

    afterEachTryDialogPlayAgain = () => {
        this.playAgainOnEachTryDialog.dispose();
        this.playAgainOnEachTryDialog = null;
        
        this.setEnableEvents(true);  

        this.clearPlayArea();

        this.goToNextStep();
    }
    
    level50Completed = () => {
        let progress = 0
        if (localStorage.getItem("level2progress")) {
            progress = parseFloat(localStorage.getItem("level2progress"));
        }

        if (progress < 0.50) {
            progress = 0.50;
            localStorage.setItem("level2progress", progress);
        }
        this.menuBar.level2ProgressBar.setProgress(progress);

        this.timer.addTimeOut(1.0, "nextstep");
    }

    level100Completed = () => {
        let progress = 0
        if (localStorage.getItem("level2progress")) {
            progress = parseFloat(localStorage.getItem("level2progress"));
        }

        progress = 1.0;
        localStorage.setItem("level2progress", progress);
        this.menuBar.level2ProgressBar.setProgress(progress);

        this.timer.addTimeOut(1.0, "nextstep");
    }

    clearPlayArea = () => {
        this.mascot.disable();
        if (this.menuBar) this.menuBar.enableReplayHand(false);
        this.removeAll4ShapesWithColor();

        this.removeTicks();

        this.removeHands();
    }

    clearPlayAreaAndNext = () => {
        this.clearPlayArea();

        this.goToNextStep();
    }

    enableTrialCriteria = () => {
        this.considerPoints = false;
        this.results = [];
        this.goToNextStep();
    }

    checkAllStraight8Values = (arr)=>{
        const prev = {
            element: null,
            count: 0
        };
        for(let i = 0; i < arr.length; i++){
            const { count, element } = prev;
            if(count === 8 && element === arr[i]){
                return true;
            };
            prev.count = element === arr[i] ? count + 1 : count;
            prev.element = arr[i];
        };
        return false;
    }

    tickTriangle = () =>{
        this.tick1 = this.getTexture2(this.assetManager.textures[2], "tick1", true, this.triangleWhite.xref, this.triangleWhite.xdisp, this.triangleWhite.yref, this.triangleWhite.ydisp, -1);
        this.tick1.setScale(0.6, 0.6);
        this.scene.add(this.tick1.texture);

        this.tick2 = this.getTexture2(this.assetManager.textures[2], "tick2", true, this.triangleBlack.xref, this.triangleBlack.xdisp, this.triangleBlack.yref, this.triangleBlack.ydisp, -1);
        this.tick2.setScale(0.6, 0.6);
        this.scene.add(this.tick2.texture);

        this.tick3 = this.getTexture2(this.assetManager.textures[2], "tick3", true, this.triangleBrown.xref, this.triangleBrown.xdisp, this.triangleBrown.yref, this.triangleBrown.ydisp, -1);
        this.tick3.setScale(0.6, 0.6);
        this.scene.add(this.tick3.texture);

        this.tick4= this.getTexture2(this.assetManager.textures[2], "tick4", true, this.triangleGreen.xref, this.triangleGreen.xdisp, this.triangleGreen.yref, this.triangleGreen.ydisp, -1);
        this.tick4.setScale(0.6, 0.6);
        this.scene.add(this.tick4.texture);
    }

    removeTicks = () =>{
        const thisTickObjects = this;
        const tickShapes = [this.tick1,this.tick2,this.tick3,this.tick4];
        let scene = this.scene;
        
        tickShapes.forEach(function (item) {
            if (item) {
                scene.remove(item.texture);
                thisTickObjects.removeAsset(item.texture);
            }
        });
    }

    removeHands = () => {
        if (this.hand1) this.scene.remove(this.hand1.texture);
        if (this.hand2) this.scene.remove(this.hand2.texture);
        if (this.hand3) this.scene.remove(this.hand3.texture);
        if (this.hand4) this.scene.remove(this.hand4.texture);
    }

    validateShapeSelection = (shapeName) => {
        this.time = 0;
        if (this.waitingFor1 == shapeName || this.waitingFor2 == shapeName || this.waitingFor3 == shapeName || this.waitingFor4 == shapeName) {
            // Right answer
            // Place right mark and move to next step 
            console.log(this.shapeResults.length) 
            //This is to remove already available tick so that it doesn't overlap
            if (this.shapeResults.length == 0){
                this.removeTicks();
            }
            //This is for right answer
            let correctMusic = new Audio('./audio/common/For_correct_answer.mp3')
        
            const thisObj = this;
            const correctFirstShape = [this.circleBrown,this.triangleBlue,this.rectangleBlack,this.squareBrown];
            const correctSecondShape = [this.circleOrange,this.rectangleYellow,this.squareOrange,this.triangleGrey];
            const correctthirdShape =   [this.circlePink,this.rectangleOrange,this.squareWhite, this.triangleYellow];
            const correctForthShape =   [this.circleBlue,this.rectanglePurple,this.squareBlue,this.trianglePink];

            if(this.waitingFor1 == shapeName && !this.checkIfClicked.includes("A")){
                correctFirstShape.forEach(function (item) {
                    if(item && item.texture.name == shapeName){
                        thisObj.tick1 = thisObj.getTexture2(thisObj.assetManager.textures[2], "tick1", true, item.xref, item.xdisp, item.yref, item.ydisp, -1);
                        thisObj.tick1.setScale(0.6, 0.6);
                        thisObj.scene.add(thisObj.tick1.texture);
                        thisObj.shapeResults.push(1);
                        thisObj.checkIfClicked.push("A");   //this is too check if the shape is already clicked
                    }
                });
                correctMusic.play();
            }else if(this.waitingFor2 == shapeName && !this.checkIfClicked.includes("B")){
                correctSecondShape.forEach(function (item) {
                    if(item && item.texture.name == shapeName){
                        thisObj.tick2 = thisObj.getTexture2(thisObj.assetManager.textures[2], "tick2", true, item.xref, item.xdisp, item.yref, item.ydisp, -1);
                        thisObj.tick2.setScale(0.6, 0.6);
                        thisObj.scene.add(thisObj.tick2.texture);
                        thisObj.shapeResults.push(1);
                        thisObj.checkIfClicked.push("B");
                    }
                });
                correctMusic.play();
            }else if(this.waitingFor3 == shapeName && !this.checkIfClicked.includes("C")){
                correctthirdShape.forEach(function (item) {
                    if(item && item.texture.name == shapeName){
                        thisObj.tick3 = thisObj.getTexture2(thisObj.assetManager.textures[2], "tick3", true, item.xref, item.xdisp, item.yref, item.ydisp, -1);
                        thisObj.tick3.setScale(0.6, 0.6);
                        thisObj.scene.add(thisObj.tick3.texture);
                        thisObj.shapeResults.push(1);
                        thisObj.checkIfClicked.push("C");
                    }
                });
                correctMusic.play();
            }else if(this.waitingFor4 == shapeName && !this.checkIfClicked.includes("D")){
                correctForthShape.forEach(function (item) {
                    if(item && item.texture.name == shapeName){
                        thisObj.tick4 = thisObj.getTexture2(thisObj.assetManager.textures[2], "tick4", true, item.xref, item.xdisp, item.yref, item.ydisp, -1);
                        thisObj.tick4.setScale(0.6, 0.6);
                        thisObj.scene.add(thisObj.tick4.texture);
                        thisObj.shapeResults.push(1);
                        thisObj.checkIfClicked.push("D");
                    }
                });
                correctMusic.play();
            }

            this.results.push(1);
            this.sdk.setCorrectAttempts(this.levelNum);

            if (this.shapeResults.length == 4)
            {
                this.timer.addTimeOut(3, "nextstep");   
                this.waitingFor1 = "";
                this.waitingFor2 = "";
                this.waitingFor3 = "";
                this.waitingFor4 = "";

                this.waitingForTap = false;
                const last4elements = this.results.slice(-4);
                const allEqual = arr => arr.every(val => val === arr[0]);
                const elements = allEqual(last4elements);
                if(!elements){
                    this.results.push(2);
                }
                this.shapeResults = [];
                this.checkIfClicked = [];
            }
        }
        else {

            // Wrong answer
            // Vibrate for 2 seconds
            const thisObject = this;
            this.allShapes = [this.circleBrown,this.circleOrange, this.circlePink, this.circleBlue, this.circleRed, this.circleBlack, this.circleGrey, this.circleWhite,this.circleGreen,this.circleYellow, 
                        this.triangleWhite, this.triangleBlack, this.triangleBrown, this.triangleGreen, this.triangleRed, this.triangleOrange,this.triangleBlue,this.triangleGrey,this.triangleYellow,this.trianglePink,
                        this.rectangleRed, this.rectanglePurple, this.rectangleBlack, this.rectangleYellow, this.rectangleOrange,this.rectangleGrey, this.rectangleWhite, this.rectanglePink, this.rectangleBlue,this.rectangleGreen,  
                        this.squareBlue, this.squareYellow, this.squareBrown, this.squarePink, this.squareWhite, this.squareOrange,this.squareBlack,this.squareRed,this.squareGrey,this.squarePurple ];

            this.allShapes.forEach(function (item) {
                if(item && item.texture.name == shapeName){
                    switch (shapeName) {
                        case shapeName:
                        {  
                            item.endLerp();
                            let pos1 = item.texture.position;
                            let pos2 = new THREE.Vector3(pos1.x + 10, pos1.y, pos1.z);
                            item.setLerp(pos1, pos2, 0.1, true, true, thisObject, 2);
                            thisObject.lerpingObjs.push(item);
                            
                        }
                        break;
                    }
            
                    
                }
            });

            //This is for wrong answer
            let wrongMusic = new Audio('./audio/common/For_the_wrong_answer.mp3')
            wrongMusic.play();

            this.voiceOutSelection();
            
            this.results.push(0);
            this.sdk.setWrongAttempts(this.levelNum);
        }
    }
    voiceOutSelection = () => {
        switch (this.waitingFor1 || this.waitingFor2 || this.waitingFor3 ||this.waitingFor4) {
            case "circlePink": case "circleOrange": case "circleBrown": case "circleBlue":
                this.playAudioPaths(1, false, 2, './audio/level2/NS_A_L2_5.mp3');
                break;
            case "squareBlue": case "squareBrown":  case "squareOrange": case "squareWhite":
                this.playAudioPaths(1, false, 2, './audio/level2/NS_A_L2_9.mp3');
                break;
            case "trianglePink": case "triangleYellow": case "triangleBlue": case "triangleGrey":
                this.playAudioPaths(1, false, 2, './audio/level2/NS_A_L2_3.mp3');
                break;
            case "rectangleBlack": case "rectanglePurple": case "rectangleYellow": case "rectangleOrange":
                this.playAudioPaths(1, false, 2, './audio/level2/NS_A_L2_7.mp3');
                break;
        }
    }
    
    validateForCompletion = () => {
        console.log("validateForCompletion: this.result: " + this.results);

        var validValue = this.checkAllStraight8Values(this.results);
        if (!validValue) {
            this.clearPlayArea();

            if(this.step == 28 || this.step == 48){
                this.step -= 17;
            }
            
            this.checkpoint = this.step;
            this.goToCheckPoint();
        }
        else {
            this.goToNextStep();
        }

    }

