    removeAll4ShapesWithColor = () =>{
        const thisObjects = this;
        const allShapes = [this.circleBrown,this.circleOrange, this.circlePink, this.circleBlue, this.circleRed, this.circleBlack, this.circleGrey, this.circleWhite,this.circleGreen,this.circleYellow, 
                        this.triangleWhite, this.triangleBlack, this.triangleBrown, this.triangleGreen, this.triangleRed, this.triangleOrange,this.triangleBlue,this.triangleGrey,this.triangleYellow,this.trianglePink,
                        this.rectangleRed, this.rectanglePurple, this.rectangleBlack, this.rectangleYellow, this.rectangleOrange,this.rectangleGrey, this.rectangleWhite, this.rectanglePink, this.rectangleBlue,this.rectangleGreen,  
                        this.squareBlue, this.squareYellow, this.squareBrown, this.squarePink, this.squareWhite, this.squareOrange,this.squareBlack,this.squareRed,this.squareGrey,this.squarePurple ];
        let scene = this.scene;
        
        allShapes.forEach(function (item) {
            if (item) {
                scene.remove(item.texture);
                thisObjects.removeAsset(item.texture);
            }
        });
         
    }
    
    arrangeHorizontally = (textures, distanceBetween, value) => {
        console.log(textures);
        let totalWidth = 0;
        for (let i = 0; i < textures.length; ++i) {
            totalWidth += textures[i].getWidth();
        }

        console.log(totalWidth);
        totalWidth += (textures.length - 1) * distanceBetween;
        console.log(totalWidth);

        let x = -totalWidth / 2;
        let y = 0;

        if(value == 1){
            y = 120;
        }else if(value == 2){
            y = -120;
        }

        for (let i = 0; i < textures.length; ++i) {
            x += textures[i].getWidth() / 2;
            textures[i].setNewPosition(0, x, 0, y);
            x += textures[i].getWidth() / 2 + distanceBetween;
        }
    }

    goToGameplay = () =>{

        this.checkpoint = 11;
        this.goToCheckPoint();
    }

    checkIfPresentationPlayedAndSkip = () =>{

        this.setEnableEvents(false);


        if(this.sdk.getPresentationCompleteForLevel(this.levelNum) == 1 && !localStorage.getItem("skipClickedAfterPrevious") && !this.skipContinue){
            if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1){
                this.menuBar.enableSkip(true)
            }
            setTimeout(() => {
                this.setEnableEvents(true);
            }, 600);
            this.goToGameplay();
        }else if(localStorage.getItem("skipClickedAfterPrevious")){
            this.checkpoint = localStorage.getItem("skipClickedAfterPrevious");
            localStorage.removeItem('skipClickedAfterPrevious');
            if(this.sdk.getIfLevelIsCompleted(this.levelNum) == 1){
                this.menuBar.enableSkip(true)
            }
            setTimeout(() => {
                this.setEnableEvents(true);
            }, 600);
            this.goToCheckPoint();
        }else{
            this.goToNextStep();
        }
        
    }

