import * as THREE from 'three'

class LevelProgressBar 
{
    constructor(scene, x, y, z){
        this.scene = scene;

        let width = 46;
        const geometry = new THREE.PlaneGeometry( width, 10 );
        const material = new THREE.MeshBasicMaterial( {color: 0xffffff, side: THREE.DoubleSide} );
        const background = new THREE.Mesh( geometry, material );

        let filGeometry = new THREE.PlaneGeometry( width, 10 );
        const fillMaterial = new THREE.MeshBasicMaterial( {color: 0x00ff00, side: THREE.DoubleSide} );
        let fill = new THREE.Mesh( filGeometry, fillMaterial );
        fill.position.add(new THREE.Vector3(width/2, 0, 0));
        
        this.fillGO = new THREE.Object3D();
        this.fillGO.add(fill);
        this.fillGO.position.x = -width/2;
        this.fillGO.scale.x = 0.0;
        
        this.loadingBar = new THREE.Object3D();
        this.loadingBar.position.x = x;
        this.loadingBar.position.y = y;
        this.loadingBar.position.z = z;        
        this.loadingBar.add(background);   
        this.loadingBar.add(this.fillGO);       
        this.scene.add( this.loadingBar );        
    }

    get obj(){
        return this.loadingBar;
    }    

    dispose(){
    }

    setProgress = (val) => {
        this.fillGO.scale.x = val;
    }
}

export default LevelProgressBar 
